import React, { useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import styled from "styled-components";
// import { Tricyclebrands } from "../../Tricyclejson";
import axios from "axios";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { useDispatch, useSelector } from "react-redux";
// import Alert from "@material-ui/lab/Alert";
import { SETTRICYCLEPROCESSINFO } from "../../../redux/action";

const TricycleTricycled = styled.div`
  height: 180px;
  width: 150px;
  cursor: pointer;
  border: 1px solid grey;
  margin: 10px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  :hover {
    background-color: #adeaea;
  }
`;
const SearchResultContainer = styled.div`
  min-height: 100px;
  background-color: tomato;
  position: absolute;
  width: 100%;
  z-index: 2;
`;
const TricycleBrandContainer = styled.div`
  background-color: #f3f2fa;
  height: 50px;
  margin: 5px;
  cursor: pointer;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const TricycleMODELSTricycleDS = (props) => {
  // "/images/mercedes.png"
  const { handleNext } = props;
  return (
    <TricycleBrandContainer
      onClick={handleNext.bind(this, props.name)}
      className="verticalCenterRow col-md-3"
    >
      <h6 style={{ textTransform: "uppercase" }}>{props.name}</h6>
      <ArrowForwardIcon fontSize="medium" />
    </TricycleBrandContainer>
  );
};
const Tricyclestep3 = (props) => {
  const dispatch = useDispatch();
  const { match, history, location } = props;
  const [TricycleBrandName, setTricycleBrandNamer] = useState("");
  const [Tricyclemodels, setTricyclemodels] = useState([]);
  const {
    state: { TricycleBrands, TricycleBrands_id },
  } = location;
  const handleTricyclestep1 = (e) => {
    e.preventDefault();
  };
  const handleNext = (data) => {
    history.push("/Tricycle-insurance-step4");
    dispatch(SETTRICYCLEPROCESSINFO({ tricycleModel: data }));
  };

  const MapTricycleModels = () => {
    return Tricyclemodels.map((xxx, index) => (
      <TricycleMODELSTricycleDS
        handleNext={handleNext}
        name={xxx.name}
        key={index}
      />
    ));
  };
  console.log(TricycleBrands, TricycleBrands_id);
  const FetchTricycleBrands = async () => {
    await axios
      .get(`/api/v1/FindTricycleModels?TricycleBrands_id=${TricycleBrands_id}`)
      .then((response) => {
        console.log(response.data);
        response.data.userData &&
          response.data.userData.length > 0 &&
          setTricyclemodels(response.data.userData);
      })
      .catch((err) => {
        if (err.message) {
          console.log(err.message);
        }
        console.log(err);
      });
  };
  const handleSearch = (value) => {
    if (!value) {
      return null;
    }
    value && setTricycleBrandNamer(value);
  };

  React.useEffect(() => {
    FetchTricycleBrands();
  }, []);
  return (
    <div id="__next">
      <HeaderFixed />
      <div className="our-vision-area ptb-100">
        <div style={{ height: "40px" }} />
        <div className="container">
          <div
            className="row align-items-center"
            style={{ justifyContent: "center", alignItems: "flex-start" }}
          >
            <div className="col-lg-10">
              <div className="vision-content">
                <div
                  className="row"
                  style={{ justifyContent: "space-between" }}
                >
                  <h1>Select Tricycle Model</h1>
                  <h5 style={{ margin: "10px" }}>
                    {TricycleBrands ? TricycleBrands : null}
                  </h5>
                  <div
                    onClick={history.goBack}
                    className="default-btn"
                    style={{ lineHeight: "20px" }}
                  >
                    <ArrowBackIcon fontSize="medium" />
                    Back
                  </div>
                </div>
                <div style={{ margin: "20px", position: "relative" }}>
                  <form className="row" onSubmit={handleTricyclestep1}>
                    <input
                      style={{ width: "100%" }}
                      type="text"
                      className="search-field"
                      placeholder="Search Tricycle Brands"
                      name="Tricyclenumber"
                      required
                      value={TricycleBrandName}
                      onChange={(e) => handleSearch(e.target.value)}
                    />
                    {/* <button className="default-btn">VIEW PRICES</button> */}
                  </form>
                </div>
                <div className="row" style={{ justifyContent: "center" }}>
                  {MapTricycleModels()}
                </div>
              </div>
              <div style={{ height: "40px" }} />
              <button
                type="submit"
                className="default-btn"
                style={{ width: "200px" }}
                onClick={handleNext.bind(this, "others")}
              >
                Others
              </button>
              <div
                className="row"
                style={{
                  justifyContent: "flex-end",
                }}
              ></div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default Tricyclestep3;
