import React, { useEffect, useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import styled from "styled-components";
import { Carbrands } from "../../Carjson";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { useHistory } from "react-router";
import { useDispatch, useSelector } from "react-redux";
// import Alert from "@material-ui/lab/Alert";
import { SETCARPROCESSINFO } from "../../../redux/action";
import axios from "axios";
const CarCard = styled.div`
  height: 180px;
  width: 150px;
  cursor: pointer;
  border: 1px solid grey;
  margin: 10px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  :hover {
    background-color: #adeaea;
  }
`;
const CarBrandContainer = styled.div`
  background-color: #fff;
  min-height: 10px;
  margin: 5px;
  cursor: pointer;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const SearchResultContainer = styled.div`
  min-height: 40px;
  background-color: rgb(255, 255, 255, 0.5);
  position: absolute;
  width: 100%;
  z-index: 2;
  padding: 10px;
  box-sizing: border-box;
`;
const CARMODELSCARDS = (props) => {
  // "/images/mercedes.png"
  const history = useHistory();
  const dispatch = useDispatch();
  const HandleNext = (data) => {
    history.push({ pathname: "/Carstep3", state: { CarBrands: props.name } });
    dispatch(SETCARPROCESSINFO({ carBrand: data }));
  };
  return (
    <CarBrandContainer
      onClick={HandleNext.bind(this, props.name)}
      className="verticalCenterRow col-md-5"
    >
      <p style={{ textTransform: "uppercase" }}>{props.name}</p>
      <ArrowForwardIcon fontSize="medium" />
    </CarBrandContainer>
  );
};
const CARBRANDS = (props) => {
  // "/images/mercedes.png"
  const history = useHistory();
  const dispatch = useDispatch();

  const HandleNext = (data) => {
    history.push({ pathname: "/Carstep3", state: { CarBrands: props.name } });
    dispatch(SETCARPROCESSINFO({ carBrand: data }));
  };
  return (
    <CarCard onClick={HandleNext.bind(this, props.name)}>
      <img
        src={props.src}
        style={{ height: "70%", objectFit: "contain" }}
        alt="carlogo"
      />
      <h6 style={{ marginTop: "auto", textTransform: "uppercase" }}>
        {props.name}
      </h6>
    </CarCard>
  );
};
const CarStep2 = (props) => {
  const { match, history } = props;
  const [carnumber, setCarnumber] = useState("");
  const [carBrandName, setcarBrandNamer] = useState("");
  const [searchResults, setsearchResult] = useState([]);
  const carProcess = useSelector(({ CarProcessReducer }) => CarProcessReducer);
  const dispatch = useDispatch();

  const handleCarstep1 = (e) => {
    e.preventDefault();
  };
  useEffect(() => {
    console.log(carProcess);
  }, []);
  const MapCars = () => {
    return Carbrands.map((xxx, index) => (
      <CARBRANDS name={xxx.name} src={xxx.src} key={index} />
    ));
  };
  const MaphResults = () => {
    return searchResults.map((xxx) => (
      <CARMODELSCARDS name={xxx.name} key={xxx._id} />
    ));
  };
  const FetchCrasBrands = async () => {
    await axios
      .get("/api/v1/listAllCars")
      .then((response) => {
        // console.log(response.data);
      })
      .catch((err) => {
        if (err.message) {
          // console.log(err.message);
        }
        // console.log(err);
      });
  };
  const FindCarModelsBySearch = async (query) => {
    await axios
      .get(`/api/v1/FindCarModelsBySearch?search=${query}`)
      .then((response) => {
        // console.log(response.data);
        response.data &&
          response.data.userData.length > 0 &&
          setsearchResult(response.data.userData);
      })
      .catch((err) => {
        if (err.message) {
          // console.log(err.message);
        }
        // console.log(err);
      });
  };
  const handleSearch = (value) => {
    setcarBrandNamer(value);
    !value && setsearchResult([]);
    if (!value) {
      return null;
    }

    value && FindCarModelsBySearch(value);
  };
  useEffect(() => {
    FetchCrasBrands();
  }, []);
  return (
    <div id="__next">
      <HeaderFixed />
      <div className="our-vision-area ptb-100">
        <div style={{ height: "40px" }} />
        <div className="container">
          <div
            className="row align-items-center"
            style={{ justifyContent: "center", alignItems: "flex-start" }}
          >
            <div className="col-lg-10">
              <div className="vision-content">
                <div
                  className="row"
                  style={{ justifyContent: "space-between" }}
                >
                  <h1>Select Car Brand</h1>{" "}
                  <div
                    onClick={history.goBack}
                    className="default-btn"
                    style={{ lineHeight: "20px" }}
                  >
                    <ArrowBackIcon fontSize="medium" />
                    Back
                  </div>
                </div>
                <div style={{ margin: "20px", position: "relative" }}>
                  <form className="row" onSubmit={handleCarstep1}>
                    <input
                      autoComplete="off"
                      style={{ width: "100%" }}
                      type="carnumber"
                      className="search-field"
                      placeholder="Search Car Brands"
                      name="carnumber"
                      required
                      value={carBrandName}
                      onChange={(e) => handleSearch(e.target.value)}
                    />
                    {/* <button className="default-btn">VIEW PRICES</button> */}
                  </form>
                  {searchResults.length > 0 ? (
                    <SearchResultContainer>
                      {MaphResults()}
                    </SearchResultContainer>
                  ) : null}
                </div>
                <div className="row" style={{ justifyContent: "center" }}>
                  {MapCars()}
                </div>
              </div>
              <div style={{ height: "40px" }} />
              <button
                type="submit"
                className="default-btn"
                style={{ width: "200px" }}
              >
                Others
              </button>
              <div
                className="row"
                style={{
                  justifyContent: "flex-end",
                }}
              ></div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default CarStep2;
