import React, { useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import { useDispatch } from "react-redux";
// import Alert from "@material-ui/lab/Alert";
import { SETTRICYCLEPROCESSINFO } from "../../../redux/action";

const TricycleStep1 = (props) => {
  const dispatch = useDispatch();
  const [Tricyclenumber, setTricyclenumber] = useState("");
  const { match, history } = props;
  const handleTricyclestep1 = (e) => {
    console.log(Tricyclenumber);
    dispatch(SETTRICYCLEPROCESSINFO({ tricycleNumber: Tricyclenumber }));
    e.preventDefault();
    history.push("/Tricycle-insurance-step2");
  };
  return (
    <div id="__next">
      <HeaderFixed />
      <div className="our-vision-area ptb-100">
        <div style={{ height: "40px" }} />
        <div className="container">
          <div
            className="row align-items-center"
            style={{ justifyContent: "center", alignItems: "flex-start" }}
          >
            <div className="col-lg-6">
              <div className="vision-content">
                <h1>Tricycle insurance quote</h1>
                {/* <h3>
                  Instant policy <br />
                  No inspection
                </h3> */}

                {/* <p>
                  Save up to <b style={{ color: "dodgerblue" }}> 80%</b> + on
                  Tricycle insurance
                </p> */}

                <div style={{ margin: "20px" }}>
                  <form className="row" onSubmit={handleTricyclestep1}>
                    <input
                      type="Tricyclenumber"
                      className="search-field"
                      placeholder="Enter Tricycle Number"
                      name="Tricyclenumber"
                      required
                      value={Tricyclenumber}
                      onChange={(e) => setTricyclenumber(e.target.value)}
                    />
                    <button className="default-btn">VIEW PRICES</button>
                  </form>
                </div>

                <div className="vision-list">
                  <ul>
                    <li>
                      <i className="bx bx-chevrons-right" />
                      Instant Policy
                    </li>
                    <li>
                      <i className="bx bx-chevrons-right" />
                      Fast Application
                    </li>
                    <li>
                      <i className="bx bx-chevrons-right" />
                      Flexible Insurance
                    </li>
                    <li>
                      <i className="bx bx-chevrons-right" />
                      No Brokers, No Upselling
                    </li>
                  </ul>
                </div>
              </div>
              <div style={{ height: "40px" }} />
              <button
                onClick={handleTricyclestep1}
                type="submit"
                className="default-btn"
              >
                proceed without Tricycle number{" "}
                <ArrowForwardIcon fontSize="medium" />
              </button>
              <div
                className="row"
                style={{
                  justifyContent: "flex-end",
                }}
              >
                <p
                  style={{
                    textAlign: "right",
                    float: "right",
                    paddingRight: "80px",
                    boxSizing: "border-box",
                    marginTop: "20px",
                    marginBottom: "30px",
                  }}
                >
                  Brand new Tricycle click{" "}
                  <b
                    onClick={handleTricyclestep1}
                    style={{ color: "dodgerblue", cursor: "pointer" }}
                  >
                    here
                  </b>{" "}
                </p>
              </div>
            </div>
            <div
              style={{
                paddingLeft: "1px",

                marginLeft: "10px",
              }}
            >
              <div
                className="about-img "
                style={{
                  animationName: "none",
                  visibility: "vissible",
                  animationDelay: "0.1s",
                }}
              >
                <img
                  style={{ maxWidth: "350px" }}
                  src="/images/tricycle2.png"
                  alt=""
                />
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default TricycleStep1;
