import React from "react";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { Link } from "react-router-dom";
import styled from "styled-components";

import FiberManualRecordIcon from "@material-ui/icons/FiberManualRecord";
const Listing = styled.ul`
  width: 100%;
  li {
    width: 100%;
    a {
      width: 100%;
    }
  }
`;
const TopMenu = () => {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <>
      <button
        style={{ position: "relative" }}
        className=" signin-btn"
        onClick={handleClick}
      >
        My Account
        <ExpandMoreIcon fontSize="small" style={{ fontSize: "15px" }} />
      </button>

      <Menu
        disableScrollLock={true}
        className="col-lg-4 top-menuDrawer"
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        style={{ marginTop: "40px" }}
        onClose={handleClose}
        PaperProps={{
          style: { width: "270px" },
        }}
      >
        <Listing>
          {/* <li>
            <Link to="/Quotes">
              <MenuItem
                className="menu-item"
                style={{ color: "#262566" }}
                onClick={handleClose}
              >
                <span>
                  <FiberManualRecordIcon
                    style={{ fontSize: "10px" }}
                    fontSize="small"
                    className="iCon"
                  />
                  Quotes
                </span>
              </MenuItem>
            </Link>
          </li> */}
          {/* <li>
            <Link to="/Dashboard">
              <MenuItem style={{ color: "#262566" }} onClick={handleClose}>
                <span>
                  <FiberManualRecordIcon
                    fontSize="small"
                    className="iCon"
                    style={{ fontSize: "10px" }}
                  />
                  My account
                </span>
              </MenuItem>
            </Link>
          </li> */}
          {/* <li>
            <Link to="/Settings">
              <MenuItem style={{ color: "#262566" }} onClick={handleClose}>
                <FiberManualRecordIcon
                  style={{ fontSize: "10px" }}
                  fontSize="small"
                  className="iCon"
                />
                Settings
              </MenuItem>
            </Link>
          </li> */}
          <li>
            <Link to="/Agents-Login">
              <MenuItem style={{ color: "#262566" }} onClick={handleClose}>
                <FiberManualRecordIcon
                  style={{ fontSize: "10px" }}
                  fontSize="small"
                  className="iCon"
                />{" "}
                Agent Log in
              </MenuItem>
            </Link>
          </li>
          <div className="verticalCenterRow">
            <Link to="/Login">
              <MenuItem style={{ color: "#262566" }} onClick={handleClose}>
                <FiberManualRecordIcon
                  style={{ fontSize: "10px" }}
                  fontSize="small"
                  className="iCon"
                />
                Log in
              </MenuItem>
            </Link>
            <Link to="/Signup">
              <MenuItem style={{ color: "#262566" }} onClick={handleClose}>
                <FiberManualRecordIcon
                  style={{ fontSize: "10px" }}
                  fontSize="small"
                  className="iCon"
                />
                Create Account
              </MenuItem>
            </Link>
          </div>
        </Listing>
      </Menu>
    </>
  );
};

export default TopMenu;
