import React, { useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import styled from "styled-components";
import { Bikebrands } from "../../Bikejson";
import axios from "axios";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { useDispatch, useSelector } from "react-redux";
// import Alert from "@material-ui/lab/Alert";
import { SETBIKEPROCESSINFO } from "../../../redux/action";

const BikeBiked = styled.div`
  height: 180px;
  width: 150px;
  cursor: pointer;
  border: 1px solid grey;
  margin: 10px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  :hover {
    background-color: #adeaea;
  }
`;
const SearchResultContainer = styled.div`
  min-height: 100px;
  background-color: tomato;
  position: absolute;
  width: 100%;
  z-index: 2;
`;
const BikeBrandContainer = styled.div`
  background-color: #f3f2fa;
  height: 50px;
  margin: 5px;
  cursor: pointer;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;

const BikeRegistrationYear = [
  { name: "2020" },
  { name: "2019" },
  { name: "2018" },
  { name: "2017" },
  { name: "2016" },
  { name: "2015" },
  { name: "2014" },
  { name: "2013" },
  { name: "2012" },
  { name: "2011" },
  { name: "2010" },
  { name: "2009" },
  { name: "2008" },
  { name: "2007" },
];

const BikeMFUELTYPES = (props) => {
  // "/images/mercedes.png"
  const { handleNext } = props;
  return (
    <BikeBrandContainer
      onClick={handleNext.bind(this, props.name)}
      className="verticalCenterRow col-md-3"
    >
      <h6 style={{ textTransform: "uppercase" }}>{props.name}</h6>
      <ArrowForwardIcon fontSize="medium" />
    </BikeBrandContainer>
  );
};
const Bikestep6 = (props) => {
  const dispatch = useDispatch();
  const { match, history, location } = props;
  const [BikeBrandName, setBikeBrandNamer] = useState("");
  const [Bikemodels, setBikemodels] = useState([]);

  const handleBikestep1 = (e) => {
    e.preventDefault();
  };
  const handleNext = (data) => {
    history.push("/bike-insurance-step7");
    dispatch(SETBIKEPROCESSINFO({ bikeRegisterYear: data }));
  };

  const MapBikeRegistrationYear = () => {
    return BikeRegistrationYear.map((xxx, index) => (
      <BikeMFUELTYPES handleNext={handleNext} name={xxx.name} key={index} />
    ));
  };

  const handleSearch = (value) => {
    if (!value) {
      return null;
    }
    value && setBikeBrandNamer(value);
  };

  return (
    <div id="__next">
      <HeaderFixed />
      <div className="our-vision-area ptb-100">
        <div style={{ height: "40px" }} />
        <div className="container">
          <div
            className="row align-items-center"
            style={{ justifyContent: "center", alignItems: "flex-start" }}
          >
            <div className="col-lg-10">
              <div className="vision-content">
                <div
                  className="row"
                  style={{ justifyContent: "space-between" }}
                >
                  <h1>Select Bike Registration Year</h1>

                  <div
                    onClick={history.goBack}
                    className="default-btn"
                    style={{ lineHeight: "20px" }}
                  >
                    <ArrowBackIcon fontSize="medium" />
                    Back
                  </div>
                </div>{" "}
                <div style={{ height: "50px" }} />
                <div className="row" style={{ justifyContent: "center" }}>
                  <BikeMFUELTYPES
                    handleNext={handleNext}
                    name="Brand New Bike"
                  />
                  {MapBikeRegistrationYear()}
                </div>{" "}
              </div>
              <div style={{ height: "60px" }} />
              <button
                type="submit"
                className="default-btn"
                style={{ width: "200px" }}
              >
                Others
              </button>
              <div
                className="row"
                style={{
                  justifyContent: "flex-end",
                }}
              ></div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default Bikestep6;
