import React from "react";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

export default function IdvMenu() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <button onClick={handleClick}>Insured Value(IDV): Select IDV:</button>
      <Menu
        disableScrollLock={true}
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <div style={{ width: "220px", padding: 10 + "px" }}>
          {" "}
          <b>Set By</b>
        </div>
        <MenuItem onClick={handleClose}>lowest IDV</MenuItem>
        <MenuItem onClick={handleClose}>Recommended IDV</MenuItem>
        <MenuItem onClick={handleClose}>Maximum IDV</MenuItem>
      </Menu>
    </>
  );
}
