import React from "react";
import Footer from "../Footer";
import Header2 from "../Header_fixed";

const Signup = () => {
  return (
    <div id="__next">
      <Header2 />
      <div className="user-area-all-style sign-up-area ptb-100">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="contact-form-action">
                <div className="form-heading text-center">
                  <h3 className="form-title">Create an account!</h3>
                  <p className="form-desc">With your social network.</p>
                </div>
                <form method="post">
                  <div className="row">
                    <div className="col-lg-4 col-md-4 col-sm-12">
                      <button className="default-btn" type="submit">
                        Google
                      </button>
                    </div>
                    <div className="col-lg-4 col-md-4 col-sm-12">
                      <button className="default-btn" type="submit">
                        Facebook
                      </button>
                    </div>

                    <div className="col-lg-6 col-sm-12">
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          name="name"
                          placeholder="First Name"
                        />
                      </div>
                    </div>
                    <div className="col-lg-6 col-sm-12">
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          name="name"
                          placeholder="Last Name"
                        />
                      </div>
                    </div>
                    <div className="col-md-12 col-sm-12">
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          name="name"
                          placeholder="Enter your Username"
                        />
                      </div>
                    </div>
                    <div className="col-md-12 col-sm-12">
                      <div className="form-group">
                        <input
                          type="email"
                          className="form-control"
                          name="email"
                          placeholder="Email Address"
                        />
                      </div>
                    </div>
                    <div className="col-md-12 col-sm-12">
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          name="password"
                          placeholder="Password"
                        />
                      </div>
                    </div>
                    <div className="col-md-12 col-sm-12">
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          name="password"
                          placeholder="Confirm Password"
                        />
                      </div>
                    </div>
                    <div className="col-md-12 col-sm-12 col-xs-12 form-condition">
                      <div className="agree-label">
                        <input type="checkbox" id="chb1" />
                        <label htmlFor="chb1">
                          I agree with iPolicyMart
                          <a href="">Privacy Policy</a>
                        </label>
                      </div>
                      <div className="agree-label">
                        <input type="checkbox" id="chb2" />
                        <label htmlFor="chb2">
                          I agree with iPolicyMart
                          <a href="">Terms &amp; Conditions</a>
                        </label>
                      </div>
                    </div>
                    <div className="col-12">
                      <button className="default-btn btn-two" type="submit">
                        Sign Up
                      </button>
                    </div>
                    <div className="col-12">
                      <p className="account-desc">
                        Already have an account?<a href="/Login">Log In</a>
                      </p>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default Signup;
