import React, { useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import { useDispatch } from "react-redux";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import ArrowBackIcon from "@material-ui/icons/ArrowLeft";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import { ArrowBackIos } from "@material-ui/icons";
import TuneIcon from "@material-ui/icons/Tune";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import styled from "styled-components";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import DoneIcon from "@material-ui/icons/Done";
import Alert from "@material-ui/lab/Alert";
import { SETHOMEINSURANCEPROCESSINFO } from "../../../redux/action";
const InsuranceCoys = styled.div`
  background-color: #fff;
  min-height: 150px;
  width: 100%;
  border-radius: 4px;
  margin-bottom: 15px;
  margin-top: 15px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const InsureLogo = styled.img`
height:80px;
object-fit;


`;
const ContentSection = styled.div`
  height: 80%;
  // background-color: tomato;
  width: 90%;
  margin-left: 5px;
  padding-left: 10px;
`;
const ButtonSection = styled.div`
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;
  background-color: tomato;
`;

const Button1 = styled.button`
  color: #31abfc;
  border: 1px solid #31abfc;
  background-color: #fff;
  border-radius: 4px;
  margin-bottom: 9px;
`;
const Button2 = styled.button`
  color: #fff;
  border: 1px solid #31abfc;
  background-color: #31abfc;
  border-radius: 4px;
`;
const Divider = styled.div`
  width: 100%;
  height: 1px;
  background-color: grey;
  margin-bottom: 10px;
  margin-top: 5px;
`;
const OptionsCard = styled.div`
  height: 250px;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  background-color: #fff;
  text-align: center;
  padding: 10px;
  button {
    width: 100%;
  }
  ul {
    text-align: left;
    cursor: pointer;
    li {
      margin-bottom: 20px;
    }
  }
`;
const Addsection = styled.div`
  min-height: 500px;
  width: 100%;
  background-color: #fff;
  margin-top: 15px;
  padding: 10px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const InsuranceCoysCard = (props) => {
  return (
    <InsuranceCoys>
      <InsureLogo src="/images/compassInsure.png" alt="img" />
      <ContentSection>
        <div className="verticalCenterRow justify-content-start">
          <div>
            <h3>
              Compass
              <br /> Insure
            </h3>
            <a href="#">Go to website</a>
          </div>
          <div className="FeaturesListing verticalCenterRow">
            <ul>
              <li>
                <CheckCircleIcon
                  fontSize="small"
                  style={{ color: "lightgreen", marginRight: "5px" }}
                />
                Passport loss covered
              </li>
              <li>
                <CheckCircleIcon
                  fontSize="small"
                  style={{ color: "lightgreen", marginRight: "5px" }}
                />
                Flight Delay covered
              </li>
            </ul>
            <ul>
              <li>
                <CheckCircleIcon
                  fontSize="small"
                  style={{ color: "lightgreen", marginRight: "5px" }}
                />
                personnal aciddents covred
              </li>
              <li>
                <CheckCircleIcon
                  fontSize="small"
                  style={{ color: "lightgreen", marginRight: "5px" }}
                />
                Flight Dealay and cancelllations
              </li>
            </ul>
          </div>
          <div
            style={{
              marginLeft: "auto",
              alignItems: "flex-end",
            }}
            className="horizontal-center-flex"
          >
            <Button1>N1,650</Button1>
            <small>PRODUCT DETAIL</small>
          </div>
        </div>
        <Divider />
        <div className="verticalCenterRow">
          <p>Covers COVID-19 related trip cancellations**</p>

          <div
            style={{ marginLeft: "auto" }}
            className="horizontal-center-flex"
          >
            <Button2>PAY NOW</Button2>
          </div>
        </div>
      </ContentSection>
      {/* <ButtonSection>
        <Button1>N1,650</Button1>
        <small>PRODUCT DETAIL</small>
        <Button2>PAY NOW</Button2>
      </ButtonSection> */}
    </InsuranceCoys>
  );
};

const HomeStep2 = (props) => {
  const { match, history } = props;
  const dispatch = useDispatch();
  const [homeOwnership, sethomeOwnership] = React.useState("");
  const [homeInsuranceType, sethomeInsuranceType] = React.useState("");
  const [errorAlert, setErrorAlert] = useState({ status: false, message: "" });

  const [formResponse, setFormresponse] = useState({
    title: "",
    firstName: "",
    surnName: "",
    email: "",
    dob: "",
    // phoneNumber: "",
    mobileNumber: "",
    cover_type: "",
    occupation: "",
    sex: "",
  });
  const handleBack = () => {
    history.goBack();
  };
  function validateEmail(email) {
    const re =
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }
  const handleNext = (e) => {
    e.preventDefault();
    if (
      // !formResponse.cover_type ||
      !formResponse.title ||
      !formResponse.firstName ||
      !formResponse.email ||
      !formResponse.surnName ||
      !formResponse.mobileNumber ||
      !formResponse.sex ||
      !formResponse.occupation ||
      !formResponse.dob
    ) {
      console.log(formResponse);
      return setErrorAlert({
        status: true,
        message: "There is an error on this page, all fields were not filled",
      });
    } else {
      setErrorAlert({
        status: false,
        message: "",
      });
    }
    if (!validateEmail(formResponse.email)) {
      return setErrorAlert({
        status: true,
        message: "Invalid email provided",
      });
    }
    history.push({
      // pathname: "/Home-insurance-checkout",
      pathname: "/HomeStep3",
    });
    dispatch(SETHOMEINSURANCEPROCESSINFO(formResponse));
  };
  return (
    <div>
      <HeaderFixed />
      <div
        className="our-vision-area ptb-100 homeSection"
        style={{ backgroundColor: "#fff" }}
      >
        <div className="container ">
          <div style={{ height: "20px" }} />
          <div className="row">
            <div className="col">
              {/* <h2>Policyholder details!</h2> */}

              <p style={{ float: "right" }}>Step2 of 3</p>
              <div style={{ height: "40px" }} />
            </div>
          </div>
          <div
            className="row"
            style={{
              backgroundColor: "rgb(243,247,253)",
              padding: "10px",
            }}
          >
            <div className="col">
              <h3>Policyholder details !</h3>
            </div>
          </div>
          <div style={{ height: "80px" }} />
          <div className="row" style={{}}>
            <form
              style={{
                width: "100%",

                justifyContent: "center",
                textAlign: "center",
              }}
              className="homeRegisterForm"
            >
              <div className="col-lg-9 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-between ">
                  <label>Title</label>
                  <div style={{ width: "100px" }} />
                  <span
                    onClick={() =>
                      setFormresponse({ ...formResponse, title: "Mr" })
                    }
                    class="radioSelector"
                  >
                    <input
                      checked={formResponse.title === "Mr"}
                      onChange={() =>
                        setFormresponse({ ...formResponse, title: "Mr" })
                      }
                      type="radio"
                      name="title"
                      placeholder="House name or number"
                    />
                    Mr
                  </span>
                  <span
                    onClick={() =>
                      setFormresponse({ ...formResponse, title: "Mrs" })
                    }
                    class="radioSelector"
                  >
                    <input
                      checked={formResponse.title === "Mrs"}
                      onChange={() =>
                        setFormresponse({ ...formResponse, title: "Mrs" })
                      }
                      type="radio"
                      name="title"
                    />
                    Mrs
                  </span>
                  <span
                    onClick={() =>
                      setFormresponse({ ...formResponse, title: "Ms" })
                    }
                    class="radioSelector"
                  >
                    <input
                      checked={formResponse.title === "Ms"}
                      onChange={() =>
                        setFormresponse({ ...formResponse, title: "Ms" })
                      }
                      type="radio"
                      name="title"
                    />
                    Ms
                  </span>
                  <span
                    onClick={() =>
                      setFormresponse({ ...formResponse, title: "Mss" })
                    }
                    class="radioSelector"
                  >
                    <input
                      checked={formResponse.title === "Mss"}
                      onChange={() =>
                        setFormresponse({ ...formResponse, title: "Mss" })
                      }
                      type="radio"
                      name="title"
                    />
                    Mss
                  </span>
                </div>
              </div>
              <div style={{ marginTop: "50px" }} />
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>First Name?</label>
                  <input
                    type="text"
                    required
                    value={formResponse.firstName}
                    onChange={(e) =>
                      setFormresponse({
                        ...formResponse,
                        firstName: e.target.value,
                      })
                    }
                    class="form-control"
                    name="name"
                    placeholder="First Name"
                  />
                </div>
              </div>
              <div style={{ marginTop: "50px" }} />
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>Surename?</label>
                  <input
                    type="text"
                    value={formResponse.surnName}
                    onChange={(e) =>
                      setFormresponse({
                        ...formResponse,
                        surnName: e.target.value,
                      })
                    }
                    required
                    class="form-control"
                    name="surename"
                    placeholder="Surename"
                  />
                </div>
              </div>
              <div style={{ marginTop: "50px" }} />
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>Email?</label>
                  <input
                    type="text"
                    value={formResponse.email}
                    onChange={(e) =>
                      setFormresponse({
                        ...formResponse,
                        email: e.target.value,
                      })
                    }
                    required
                    class="form-control"
                    name="email"
                    placeholder="Email"
                  />
                </div>
              </div>
              <div style={{ marginTop: "50px" }} />
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>Phone number?</label>
                  <input
                    type="text"
                    required
                    class="form-control"
                    name="phoneNumber"
                    placeholder="Phone number"
                    value={formResponse.mobileNumber}
                    onChange={(e) =>
                      setFormresponse({
                        ...formResponse,
                        mobileNumber: e.target.value,
                      })
                    }
                  />
                </div>
              </div>
              <div style={{ height: "50x" }} />
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>Date of birth</label>
                  <input
                    type="date"
                    required
                    class="form-control"
                    name="date"
                    placeholder="Date of birth"
                    value={formResponse.dob}
                    onChange={(e) =>
                      setFormresponse({
                        ...formResponse,
                        dob: e.target.value,
                      })
                    }
                  />
                </div>
              </div>
              <div style={{ height: "80px" }} />
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-between ">
                  <label>Sex</label>
                  <div style={{ width: "50px" }} />

                  <span
                    onClick={() =>
                      setFormresponse({ ...formResponse, sex: "Male" })
                    }
                    class="radioSelector"
                  >
                    <input
                      checked={formResponse.sex === "Male"}
                      onChange={() =>
                        setFormresponse({ ...formResponse, sex: "Male" })
                      }
                      type="radio"
                      name="sex"
                      placeholder="House name or number"
                    />
                    Male
                  </span>
                  <span
                    onClick={() =>
                      setFormresponse({ ...formResponse, sex: "Female" })
                    }
                    class="radioSelector"
                  >
                    <input
                      checked={formResponse.sex === "Female"}
                      onChange={() =>
                        setFormresponse({ ...formResponse, sex: "Female" })
                      }
                      type="radio"
                      name="sex"
                    />
                    Female
                  </span>
                </div>
              </div>
              <div style={{ height: "80px" }} />
              <div className="col-lg-9 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-between ">
                  <label>Your occupation</label>
                  <div style={{ width: "100px" }} />
                  <span
                    onClick={() =>
                      setFormresponse({
                        ...formResponse,
                        occupation: "Employed",
                      })
                    }
                    class="radioSelector"
                  >
                    <input
                      checked={formResponse.occupation === "Employed"}
                      onChange={() =>
                        setFormresponse({
                          ...formResponse,
                          occupation: " Employed",
                        })
                      }
                      type="radio"
                      name="occupation"
                      placeholder="House name or number"
                    />
                    Employed
                  </span>
                  <span
                    onClick={() =>
                      setFormresponse({
                        ...formResponse,
                        occupation: "Retired",
                      })
                    }
                    class="radioSelector"
                  >
                    <input
                      checked={formResponse.occupation === "Retired"}
                      onChange={() =>
                        setFormresponse({
                          ...formResponse,
                          occupation: "Retired",
                        })
                      }
                      type="radio"
                      name="occupation"
                    />
                    Retired
                  </span>
                  <span
                    onClick={() =>
                      setFormresponse({
                        ...formResponse,
                        occupation: "Self Employed",
                      })
                    }
                    class="radioSelector"
                  >
                    <input
                      checked={formResponse.occupation === "Self Employed"}
                      onChange={() =>
                        setFormresponse({
                          ...formResponse,
                          occupation: "Self Employed",
                        })
                      }
                      type="radio"
                      name="occupation"
                    />
                    Self Employed
                  </span>
                  <span
                    onClick={() =>
                      setFormresponse({
                        ...formResponse,
                        occupation: "Others",
                      })
                    }
                    class="radioSelector"
                  >
                    <input
                      checked={formResponse.occupation === "Others"}
                      onChange={() =>
                        setFormresponse({
                          ...formResponse,
                          occupation: "Others",
                        })
                      }
                      type="radio"
                      name="occupation"
                    />
                    Others
                  </span>
                </div>
              </div>

              {homeOwnership === "owned" ? (
                <div className="col-lg-8 col-sm-13">
                  <div class="form-group verticalCenterRow justify-content-lg-around ">
                    <label>I would like to insure?</label>
                    <span
                      onClick={() => sethomeInsuranceType("buildingOnly")}
                      class="radioSelector"
                    >
                      <input
                        checked={homeInsuranceType === "buildingOnly"}
                        onChange={() => sethomeInsuranceType("buildingOnly")}
                        type="radio"
                        name="homeInsuranceType"
                      />
                      Buildings only
                    </span>
                    <span
                      onClick={() => sethomeInsuranceType("contentOnly")}
                      class="radioSelector"
                    >
                      <input
                        checked={homeInsuranceType === "contentOnly"}
                        onChange={() => sethomeInsuranceType("contentOnly")}
                        type="radio"
                        name="homeInsuranceType"
                      />{" "}
                      Contents only
                    </span>
                    <span
                      onClick={() => sethomeInsuranceType("buildingAndContent")}
                      class="radioSelector"
                    >
                      <input
                        checked={homeInsuranceType === "buildingAndContent"}
                        onChange={() =>
                          sethomeInsuranceType("buildingAndContent")
                        }
                        type="radio"
                        name="homeInsuranceType"
                      />{" "}
                      The buildings and contents
                      <br />
                      (Combined Insurance )
                    </span>
                  </div>
                </div>
              ) : null}
              <div style={{ height: "80px" }} />
              {errorAlert.status ? (
                <div className="col-lg-6">
                  <Alert style={{ marginBottom: "10px" }} severity="error">
                    {errorAlert.message}
                  </Alert>
                </div>
              ) : null}
              <div style={{ height: "80px" }} />
              <button
                onClick={handleBack}
                class=""
                style={{
                  backgroundColor: "#fff",
                  color: "dodgerblue",
                  marginRight: "30px",
                }}
              >
                <ArrowBackIos fontSize="medium" />
                Back
              </button>
              <button onClick={handleNext} type="submit" class="default-btn ">
                Get Quotes <ArrowForwardIosIcon fontSize="medium" />
              </button>
            </form>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default HomeStep2;
