import React, { useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import ArrowBackIcon from "@material-ui/icons/ArrowLeft";
import axios from "axios";

import TuneIcon from "@material-ui/icons/Tune";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import styled from "styled-components";
import { useHistory } from "react-router-dom";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import { CircularProgress } from "@material-ui/core";
import DoneIcon from "@material-ui/icons/Done";
import PlanMenu from "../../PlanMenu";
import IdvMenu from "../../IdvMenu";
import InsurerMenu from "../../insurerMenu";
function numberWithCommas(x) {
  return x && x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}
const InsuranceCoys = styled.div`
  background-color: #fff;
  min-height: 150px;
  width: 100%;
  border-radius: 4px;
  margin-bottom: 15px;
  margin-top: 15px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const InsureLogo = styled.img`
height:80px;
object-fit;


`;
const ContentSection = styled.div`
  height: 80%;
  // background-color: tomato;
  width: 90%;
  margin-left: 5px;
  padding-left: 10px;
  div {
    div {
      h3 {
        max-width: 150px;
      }
    }
  }
`;
const ButtonSection = styled.div`
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;
  background-color: tomato;
`;

const Button1 = styled.button`
  color: #31abfc;
  border: 1px solid #31abfc;
  background-color: #fff;
  border-radius: 4px;
  margin-bottom: 9px;
`;
const Button2 = styled.button`
  color: #fff;
  border: 1px solid #31abfc;
  background-color: #31abfc;
  border-radius: 4px;
`;
const Divider = styled.div`
  width: 1px;
  height: 100px;
  background-color: grey;
  margin-bottom: 10px;
  margin-top: 5px;
  margin-left: 10%;
`;
const OptionsCard = styled.div`
  height: 150px;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  background-color: #fff;
  text-align: center;
  padding: 10px;
  button {
    width: 100%;
  }
  h5 {
    color: rgb(138, 218, 180);
    text-align: left;
  }
  ul {
    text-align: left;
    cursor: pointer;

    li {
      margin-bottom: 8px;
      font-weight: 700;
    }
  }
`;
const Addsection = styled.div`
  min-height: 500px;
  width: 100%;
  background-color: #fff;
  margin-top: 15px;
  padding: 10px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const InsuranceCoysCard = (props) => {
  const {
    info: { logoImg, name, price, idv },
  } = props;
  const history = useHistory();
  const handlePaynow = () => {
    history.push({ pathname: "/Checkout", state: props.info });
  };
  return (
    <InsuranceCoys>
      <InsureLogo src={logoImg} alt="img" />

      <ContentSection>
        <div className="verticalCenterRow justify-content-start">
          <div>
            <h3>{name}</h3>
            <a href="#">14 Cashless Garages</a>
          </div>
          <Divider />
          <div className="FeaturesListing CarInsuranceAddons verticalCenterRow">
            <ul>
              <li>IDV: {idv ? `=N=` + numberWithCommas(idv) : null}</li>
              <li>Addons</li>
              <li>
                Zero Dep: N7,578 +{" "}
                <b style={{ color: "dodgerblue", cursor: "pointer" }}>ADD</b>
              </li>
            </ul>
            <ul></ul>
          </div>
          <div
            style={{
              marginLeft: "auto",
              alignItems: "flex-end",
            }}
            className="horizontal-center-flex"
          >
            <Button1>N{numberWithCommas(price)}</Button1>
            <small>PRODUCT DETAIL</small>
          </div>
        </div>
        {/* <Divider /> */}
        <div className="verticalCenterRow">
          <div
            style={{ marginLeft: "auto" }}
            className="horizontal-center-flex"
          >
            <Button2 onClick={handlePaynow}>PAY NOW</Button2>
          </div>
        </div>
        <div className="features-second">
          <ul>
            <li>Own Damage cover</li>
            <li>Reimbursement within 7 working days for non cashless</li>
            <li>Self Video Claims</li>
          </ul>
        </div>
      </ContentSection>
      {/* <ButtonSection>
        <Button1>N1,650</Button1>
        <small>PRODUCT DETAIL</small>
        <Button2>PAY NOW</Button2>
      </ButtonSection> */}
    </InsuranceCoys>
  );
};

const Carstep8 = (props) => {
  const { match, history } = props;
  const [insuranceEntity, setInsuranceEntity] = React.useState([]);
  const [fetching, setFetching] = React.useState(false);

  const handleBack = () => {
    history.goBack();
  };
  const filterInsuranceEntity = (value) => {
    if (value === "all") {
      return FetchInsuranceEntity();
    }
    console.log(typeof value);
    value &&
      insuranceEntity.map((xxx) =>
        xxx.name === value ? setInsuranceEntity([xxx]) : null
      );
  };

  const filterInsuranceEntityByIDV = () => {};
  var FetchInsuranceEntity = async () => {
    console.log("called");
    setFetching(true);
    await axios
      .get(`/api/v1/fetchinsuranceEntity`)
      .then((response) => {
        setFetching(false);
        console.log(response.data);
        response.data.userData &&
          response.data.userData.length > 0 &&
          setInsuranceEntity(response.data.userData);
      })
      .catch((err) => {
        setFetching(false);
        if (err.message) {
          console.log(err.message);
        }
        console.log(err);
      });
  };

  React.useEffect(() => {
    FetchInsuranceEntity();
  }, []);
  const MapInsuranceEntity = () => {
    return insuranceEntity.map((xxx, index) => (
      <InsuranceCoysCard info={xxx} key={index} />
    ));
  };
  return (
    <div>
      <HeaderFixed />
      <div className="our-vision-area ptb-100 PricesSection">
        <div className="PricesHeaderSection row justify-content-center  align-items-center ">
          <p>
            13 Own Damage Plans Cover damages caused to your car only and not
            third party
          </p>
        </div>
        <div className="container ">
          <div style={{ height: "20px" }} />
          <div className="row justify-content-center">
            <div className="col-lg-9 col-sm-10 insurancCoys align-items-center">
              <div className="verticalCenterRow insuranceCoysHeader ">
                <div className="col-lg-1 col-sm-4">
                  <button>
                    <TuneIcon fontSize="medium" />
                  </button>
                </div>
                <div className="col-lg-3 col-sm-4">
                  <InsurerMenu
                    insuranceEntity={insuranceEntity}
                    filterInsuranceEntity={filterInsuranceEntity}
                  />
                </div>
                <div className="col-lg-4 col-sm-4">
                  <IdvMenu />
                </div>
                <div>
                  {/* <button>
                    Plan Type
                    <ExpandMoreIcon
                      fontSize="medium"
                      style={{ marginLeft: "10px" }}
                    />
                  </button> */}
                  <PlanMenu />
                </div>
              </div>
              {fetching ? (
                <CircularProgress
                  color="secondary"
                  style={{ marginLeft: "45%" }}
                />
              ) : (
                MapInsuranceEntity()
              )}
              {/* <InsuranceCoysCard />
              <InsuranceCoysCard />
              <InsuranceCoysCard />
              <InsuranceCoysCard />
              <InsuranceCoysCard /> */}
            </div>
            <div className="col-lg-3 col-sm-6 insurancPlans">
              <OptionsCard>
                <h5>ZERO HASSLE</h5>
                <ul>
                  <li>-No medical required</li>
                  <li>-Tele verification</li>
                  <li>-Instant Insurance</li>
                </ul>
              </OptionsCard>
              <Addsection>
                <h3 style={{ color: "grey", marginTop: "50%" }}>Add Here</h3>
              </Addsection>
            </div>
          </div>
          <div className="disclaimer-Section">
            <h6>Disclaimer</h6>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Quis
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Quis
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
            </p>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default Carstep8;
