import React from "react";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

export default function AgeMenu() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <button onClick={handleClick}>Property Age:</button>
      <Menu
        disableScrollLock={true}
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <div style={{ width: "220px", padding: 10 + "px" }}>
          {" "}
          <b>Property Age</b>
        </div>
        <MenuItem onClick={handleClose}>1-5years</MenuItem>
        <MenuItem onClick={handleClose}>5-10 Years</MenuItem>
        <MenuItem onClick={handleClose}>10-15 Years</MenuItem>
        <MenuItem onClick={handleClose}>15-20 Years</MenuItem>
        <MenuItem onClick={handleClose}>20-25 Years</MenuItem>
      </Menu>
    </>
  );
}
