import styled from "styled-components";
import { useHistory } from "react-router-dom";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import { CircularProgress } from "@material-ui/core";
import DoneIcon from "@material-ui/icons/Done";

function numberWithCommas(x) {
  return x && x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}
const InsuranceCoys = styled.div`
  background-color: #fff;
  min-height: 150px;
  width: 100%;
  border-radius: 4px;
  margin-bottom: 15px;
  margin-top: 15px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const InsureLogo = styled.img`
height:80px;
object-fit;


`;
const ContentSection = styled.div`
  height: 80%;
  // background-color: tomato;
  width: 90%;
  margin-left: 5px;
  padding-left: 10px;
  div {
    div {
      h3 {
        max-width: 150px;
      }
    }
  }
`;
const ButtonSection = styled.div`
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;
  background-color: tomato;
`;

const Button1 = styled.button`
  color: #31abfc;
  border: 1px solid #31abfc;
  background-color: #fff;
  border-radius: 4px;
  margin-bottom: 9px;
`;
const Button2 = styled.button`
  color: #fff;
  border: 1px solid #31abfc;
  background-color: #31abfc;
  border-radius: 4px;
`;
const Divider = styled.div`
  width: 1px;
  height: 100px;
  background-color: grey;
  margin-bottom: 10px;
  margin-top: 5px;
  margin-left: 10%;
`;
const OptionsCard = styled.div`
  height: 150px;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  background-color: #fff;
  text-align: center;
  padding: 10px;
  button {
    width: 100%;
  }
  h5 {
    color: rgb(138, 218, 180);
    text-align: left;
  }
  ul {
    text-align: left;
    cursor: pointer;

    li {
      margin-bottom: 8px;
      font-weight: 700;
    }
  }
`;
const MainPage = styled.div`
  min-height: 450px;
  position: relative;
  width: 100%;
  background-color: #fff;
  margin-top: 60px;
  padding: 20px;
  padding-top: 80px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;

const SuccessPage = (props) => {
  const {
    match,
    history,
    location: { state },
  } = props;

  const handleBack = () => {
    history.goBack();
  };

  return (
    <div>
      <HeaderFixed />
      <div className="our-vision-area ptb-100 PricesSection">
        <div className="container ">
          <div className="row justify-content-center">
            <div className="col-lg-7 col-sm-10 insurancCoys align-items-center">
              {/* here */}
              <MainPage>
                <CheckCircleIcon
                  style={{
                    color: "lime",
                    fontSize: "100px",
                    position: "absolute",
                    top: -50,
                  }}
                />
                <h1>SUCCESS !</h1>
                <div className="col-lg-6 col-sm-9">
                  <p>{state}</p>
                </div>
                <div
                  // style={{ marginTop: "auto" }}
                  className="col-lg-5 col-sm-9"
                >
                  <button
                    onClick={handleBack}
                    style={{ width: "100%" }}
                    className="default-btn"
                  >
                    ok
                  </button>
                </div>
              </MainPage>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default SuccessPage;
