import React, { useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import { useDispatch } from "react-redux";
// import Alert from "@material-ui/lab/Alert";
import { SETCARPROCESSINFO } from "../../../redux/action";

const CarStep1 = (props) => {
  const dispatch = useDispatch();
  const [carnumber, setCarnumber] = useState("");
  const { match, history } = props;
  const handleCarstep1 = (e) => {
    dispatch(SETCARPROCESSINFO({ carNumber: carnumber }));
    e.preventDefault();
    history.push("/CarStep2");
  };
  return (
    <div id="__next">
      <HeaderFixed />
      <div className="our-vision-area ptb-100">
        <div style={{ height: "40px" }} />
        <div className="container">
          <div
            className="row align-items-center"
            style={{ justifyContent: "center", alignItems: "flex-start" }}
          >
            <div className="col-lg-6">
              <div className="vision-content">
                <h1>Car insurance</h1>
                <h3>Renew in 2 Minutes</h3>

                <p>
                  Save up to <b style={{ color: "dodgerblue" }}> 80%</b> + on
                  car insurance
                </p>

                <div style={{ margin: "20px" }}>
                  <form className="row" onSubmit={handleCarstep1}>
                    <input
                      type="carnumber"
                      className="search-field"
                      placeholder="Enter Car Number"
                      name="carnumber"
                      required
                      value={carnumber}
                      onChange={(e) => setCarnumber(e.target.value)}
                    />
                    <button className="default-btn">VIEW PRICES</button>
                  </form>
                </div>

                <div className="vision-list">
                  <ul>
                    <li>
                      <i className="bx bx-chevrons-right" />
                      Save Money
                    </li>
                    <li>
                      <i className="bx bx-chevrons-right" />
                      Fast Application
                    </li>
                    <li>
                      <i className="bx bx-chevrons-right" />
                      Flexible Insurance
                    </li>
                    <li>
                      <i className="bx bx-chevrons-right" />
                      No Brokers, No Upselling
                    </li>
                    <li>
                      <i className="bx bx-chevrons-right" />
                      Investment Planning
                    </li>
                    <li>
                      <i className="bx bx-chevrons-right" />
                      Professional Advisor
                    </li>
                  </ul>
                </div>
              </div>
              <div style={{ height: "40px" }} />
              <button
                onClick={handleCarstep1}
                type="submit"
                className="default-btn"
              >
                proceed without car number{" "}
                <ArrowForwardIcon fontSize="medium" />
              </button>
              <div
                className="row"
                style={{
                  justifyContent: "flex-end",
                }}
              >
                <p
                  style={{
                    textAlign: "right",
                    float: "right",
                    paddingRight: "80px",
                    boxSizing: "border-box",
                    marginTop: "20px",
                    marginBottom: "30px",
                  }}
                >
                  Brand new car click{" "}
                  <b
                    onClick={handleCarstep1}
                    style={{ color: "dodgerblue", cursor: "pointer" }}
                  >
                    here
                  </b>{" "}
                </p>
              </div>
            </div>
            <div
              style={{
                paddingLeft: "1px",

                marginLeft: "10px",
              }}
            >
              <div
                className="about-img "
                style={{
                  animationName: "none",
                  visibility: "vissible",
                  animationDelay: "0.1s",
                }}
              >
                <img src="images/carInsurance.png" alt="Image" />
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default CarStep1;
