import React, { useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import styled from "styled-components";
import { Tricyclebrands } from "../../Tricyclejson";
import axios from "axios";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { useDispatch, useSelector } from "react-redux";
// import Alert from "@material-ui/lab/Alert";
import { SETTRICYCLEPROCESSINFO } from "../../../redux/action";
import Alert from "@material-ui/lab/Alert";

const TricycleTricycled = styled.div`
  height: 180px;
  width: 150px;
  cursor: pointer;
  border: 1px solid grey;
  margin: 10px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  :hover {
    background-color: #adeaea;
  }
`;
const SearchResultContainer = styled.div`
  min-height: 100px;
  background-color: tomato;
  position: absolute;
  width: 100%;
  z-index: 2;
`;
const TricycleBrandContainer = styled.div`
  background-color: #f3f2fa;
  height: 50px;
  margin: 5px;
  cursor: pointer;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const Input = styled.input`
  background-color: #f3f2fa;
  border: none;
  height: 50px;
  padding: 10px;
  margin: 4px;
  box-sizing: border-box;
  &:focus {
    outline: none;
    border-color: #9ecaed;
    box-shadow: 0 0 10px #9ecaed;
  }
`;
const TricycleRegistrationYear = [
  { name: "2020" },
  { name: "2019" },
  { name: "2018" },
  { name: "2017" },
  { name: "2016" },
  { name: "2015" },
  { name: "2014" },
  { name: "2013" },
  { name: "2012" },
  { name: "2011" },
  { name: "2010" },
  { name: "2009" },
  { name: "2008" },
  { name: "2007" },
];

const Tricyclestep7 = (props) => {
  const dispatch = useDispatch();
  const { match, history, location } = props;
  const [TricycleBrandName, setTricycleBrandNamer] = useState("");
  const [Tricyclemodels, setTricyclemodels] = useState([]);
  const [errorAlert, setErrorAlert] = useState({ status: false, message: "" });
  const [userDetails, setUserDetails] = React.useState({
    fullName: "",
    mobileNumber: "",
    email: "",
  });
  function validateEmail(email) {
    const re =
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }
  const handleNext = (data) => {
    if (
      !userDetails["fullName"] ||
      !userDetails["mobileNumber"] ||
      !userDetails["email"]
    ) {
      return setErrorAlert({
        status: true,
        message: "all details are required",
      });
    }
    if (!validateEmail(userDetails["email"])) {
      return setErrorAlert({
        status: true,
        message: "Invalid email provided",
      });
    }

    history.push("/Tricycle-insurance-step8");
    dispatch(SETTRICYCLEPROCESSINFO(userDetails));
  };

  const handleSearch = (value) => {
    if (!value) {
      return null;
    }
    value && setTricycleBrandNamer(value);
  };

  return (
    <div id="__next">
      <HeaderFixed />
      <div className="our-vision-area ptb-100">
        <div style={{ height: "40px" }} />
        <div className="container">
          <div
            className="row align-items-center"
            style={{ justifyContent: "center", alignItems: "flex-start" }}
          >
            <form className="col-lg-10">
              <div className="vision-content">
                <div
                  className="row"
                  style={{ justifyContent: "space-between" }}
                >
                  <h1>Please Fill Your Details</h1>

                  <div
                    onClick={history.goBack}
                    className="default-btn"
                    style={{ lineHeight: "20px" }}
                  >
                    <ArrowBackIcon fontSize="medium" />
                    Back
                  </div>
                </div>{" "}
                <div style={{ height: "50px" }} />
                <div className="col-lg-4">
                  {errorAlert.status ? (
                    <Alert style={{ marginBottom: "10px" }} severity="error">
                      {errorAlert.message}
                    </Alert>
                  ) : null}
                </div>
                <div className="col-lg-8">
                  <div className="row" style={{ marginBottom: "20px" }}>
                    <div className={`col-lg-6 col-sm-6 verticalCenterRow  `}>
                      <Input
                        style={{ width: "100%" }}
                        type="text"
                        required
                        placeholder="Enter Full Name"
                        value={userDetails.fullName}
                        onChange={(e) =>
                          setUserDetails({
                            ...userDetails,
                            fullName: e.target.value,
                          })
                        }
                      />
                    </div>
                    <div className={`col-lg-6 col-sm-6 verticalCenterRow  `}>
                      <Input
                        value={userDetails.mobileNumber}
                        style={{ width: "100%" }}
                        type="text"
                        required
                        placeholder="Enter Mobile Number"
                        onChange={(e) =>
                          setUserDetails({
                            ...userDetails,
                            mobileNumber: e.target.value,
                          })
                        }
                      />
                    </div>
                  </div>{" "}
                  <div style={{ width: "100%" }}>
                    <Input
                      style={{ width: "100%" }}
                      type="email"
                      required
                      placeholder="Enter Email"
                      value={userDetails.email}
                      onChange={(e) =>
                        setUserDetails({
                          ...userDetails,
                          email: e.target.value,
                        })
                      }
                    />
                  </div>
                </div>
              </div>
              <div style={{ height: "10px" }} />

              <div style={{ height: "50px" }} />
              <button
                onClick={handleNext}
                className="default-btn"
                style={{ width: "200px" }}
                type="submit"
                value="submit"
              >
                View Prices
              </button>
              <div
                className="row"
                style={{
                  justifyContent: "flex-end",
                }}
              ></div>
            </form>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default Tricyclestep7;
