import React, { useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import styled from "styled-components";
import { Carbrands } from "../../Carjson";
import axios from "axios";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { useDispatch, useSelector } from "react-redux";
// import Alert from "@material-ui/lab/Alert";
import { SETCARPROCESSINFO } from "../../../redux/action";

const CarCard = styled.div`
  height: 180px;
  width: 150px;
  cursor: pointer;
  border: 1px solid grey;
  margin: 10px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  :hover {
    background-color: #adeaea;
  }
`;
const SearchResultContainer = styled.div`
  min-height: 100px;
  background-color: tomato;
  position: absolute;
  width: 100%;
  z-index: 2;
`;
const CarBrandContainer = styled.div`
  background-color: #f3f2fa;
  height: 50px;
  margin: 5px;
  cursor: pointer;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;

const manualCarData = [{ name: "Manual Varient" }];

const AutomaticCarData = [{ name: "Automatic Varient" }];

const CARMFUELTYPES = (props) => {
  // "/images/mercedes.png"
  const { handleNext } = props;
  return (
    <CarBrandContainer
      onClick={handleNext.bind(this, props.name)}
      className="verticalCenterRow col-md-4"
    >
      <h6 style={{ textTransform: "uppercase" }}>{props.name}</h6>
      <ArrowForwardIcon fontSize="medium" />
    </CarBrandContainer>
  );
};
const Carstep5 = (props) => {
  const dispatch = useDispatch();
  const { match, history, location } = props;
  const [carBrandName, setcarBrandNamer] = useState("");
  const [carmodels, setCarmodels] = useState([]);

  const handleCarstep1 = (e) => {
    e.preventDefault();
  };
  const handleNext = (data) => {
    history.push("/Carstep6");
    dispatch(SETCARPROCESSINFO({ carVariant: data }));
  };

  const MapCarManualType = () => {
    return manualCarData.map((xxx, index) => (
      <CARMFUELTYPES handleNext={handleNext} name={xxx.name} key={index} />
    ));
  };
  const MapCarFuelAutomatic = () => {
    return AutomaticCarData.map((xxx, index) => (
      <CARMFUELTYPES handleNext={handleNext} name={xxx.name} key={index} />
    ));
  };

  const handleSearch = (value) => {
    if (!value) {
      return null;
    }
    value && setcarBrandNamer(value);
  };

  return (
    <div id="__next">
      <HeaderFixed />
      <div className="our-vision-area ptb-100">
        <div style={{ height: "40px" }} />
        <div className="container">
          <div
            className="row align-items-center"
            style={{ justifyContent: "center", alignItems: "flex-start" }}
          >
            <div className="col-lg-10">
              <div className="vision-content">
                <div
                  className="row"
                  style={{ justifyContent: "space-between" }}
                >
                  <h1>Select Car Variant</h1>

                  <div
                    onClick={history.goBack}
                    className="default-btn"
                    style={{ lineHeight: "20px" }}
                  >
                    <ArrowBackIcon fontSize="medium" />
                    Back
                  </div>
                </div>{" "}
                <div style={{ height: "50px" }} />
                <h3>Automatic Variants</h3>
                <div className="row" style={{ justifyContent: "center" }}>
                  {MapCarFuelAutomatic()}
                </div>{" "}
                <h3>Manual Variants</h3>
                <div className="row" style={{ justifyContent: "center" }}>
                  {MapCarManualType()}
                </div>
              </div>
              <div style={{ height: "60px" }} />
              {/* <button
                type="submit"
                className="default-btn"
                style={{ width: "200px" }}
                onClick={handleNext.bind(this, "others")}
              >
                Others
              </button> */}
              <div
                className="row"
                style={{
                  justifyContent: "flex-end",
                }}
              ></div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default Carstep5;
