import React, { useEffect, useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import styled from "styled-components";
import { Bikebrands } from "../../Bikejson";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { useHistory } from "react-router";
import { useDispatch, useSelector } from "react-redux";
// import Alert from "@material-ui/lab/Alert";
import { SETBIKEPROCESSINFO } from "../../../redux/action";
import axios from "axios";
const BikeBiked = styled.div`
  height: 180px;
  width: 150px;
  cursor: pointer;
  border: 1px solid grey;
  margin: 10px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  :hover {
    background-color: #adeaea;
  }
`;
const BikeBrandContainer = styled.div`
  background-color: #fff;
  min-height: 10px;
  margin: 5px;
  cursor: pointer;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const SearchResultContainer = styled.div`
  min-height: 40px;
  background-color: rgb(255, 255, 255, 0.5);
  position: absolute;
  width: 100%;
  z-index: 2;
  padding: 10px;
  box-sizing: border-box;
`;
const BikeMODELSBikeDS = (props) => {
  // "/images/mercedes.png"
  const history = useHistory();
  const dispatch = useDispatch();
  const HandleNext = (data) => {
    console.log(props);
    history.push({
      pathname: "/bike-insurance-step3",
      state: { BikeBrands: props.name, BikeBrands_id: props.id },
    });
    dispatch(SETBIKEPROCESSINFO({ bikeBrand: data }));
  };
  return (
    <BikeBrandContainer
      onClick={HandleNext.bind(this, props.name)}
      className="verticalCenterRow col-md-5"
    >
      <p style={{ textTransform: "uppercase" }}>{props.name}</p>
      <ArrowForwardIcon fontSize="medium" />
    </BikeBrandContainer>
  );
};
const BikeBRANDS = (props) => {
  // "/images/mercedes.png"
  const history = useHistory();
  const dispatch = useDispatch();

  const HandleNext = (data) => {
    history.push({
      pathname: "/bike-insurance-step3",
      state: { BikeBrands: props.name, BikeBrands_id: props.id },
    });
    dispatch(SETBIKEPROCESSINFO({ bikeBrand: data }));
  };
  return (
    <BikeBiked onClick={HandleNext.bind(this, props.name)}>
      <img
        src={props.src}
        style={{ height: "70%", objectFit: "contain" }}
        alt="Bikelogo"
      />
      <h6 style={{ marginTop: "auto", textTransform: "uppercase" }}>
        {props.name}
      </h6>
    </BikeBiked>
  );
};
const BikeStep2 = (props) => {
  const { match, history } = props;
  const [Bikenumber, setBikenumber] = useState("");
  const [BikeBrandName, setBikeBrandNamer] = useState("");
  const [searchResults, setsearchResult] = useState([]);
  const BikeProcess = useSelector(
    ({ BikeProcessReducer }) => BikeProcessReducer
  );
  const dispatch = useDispatch();

  const handleBikestep1 = (e) => {
    e.preventDefault();
  };
  useEffect(() => {
    console.log(BikeProcess);
  }, []);
  const MapBikes = () => {
    return Bikebrands.map((xxx, index) => (
      <BikeBRANDS name={xxx.name} src={xxx.src} key={index} id={xxx.id} />
    ));
  };
  const MaphResults = () => {
    return searchResults.map((xxx) => (
      <BikeMODELSBikeDS name={xxx.name} id={xxx.id} key={xxx._id} />
    ));
  };
  const FetchCrasBrands = async () => {
    await axios
      .get("/api/v1/ListAllBikes")
      .then((response) => {
        console.log(response.data);
      })
      .catch((err) => {
        if (err.message) {
          // console.log(err.message);
        }
        // console.log(err);
      });
  };
  const FindBikeModelsBySearch = async (query) => {
    console.log(query);
    await axios
      .get(`/api/v1/FindBikeModelsBySearch?search=${query}`)
      .then((response) => {
        // console.log(response.data);
        response.data &&
          response.data.userData.length > 0 &&
          setsearchResult(response.data.userData);
      })
      .catch((err) => {
        if (err.message) {
          // console.log(err.message);
        }
        // console.log(err);
      });
  };
  const handleSearch = (value) => {
    setBikeBrandNamer(value);
    !value && setsearchResult([]);
    if (!value) {
      return null;
    }

    value && FindBikeModelsBySearch(value);
  };
  useEffect(() => {
    FetchCrasBrands();
  }, []);
  return (
    <div id="__next">
      <HeaderFixed />
      <div className="our-vision-area ptb-100">
        <div style={{ height: "40px" }} />
        <div className="container">
          <div
            className="row align-items-center"
            style={{ justifyContent: "center", alignItems: "flex-start" }}
          >
            <div className="col-lg-10">
              <div className="vision-content">
                <div
                  className="row"
                  style={{ justifyContent: "space-between" }}
                >
                  <h1>Select a Manufacturer</h1>{" "}
                  <div
                    onClick={history.goBack}
                    className="default-btn"
                    style={{ lineHeight: "20px" }}
                  >
                    <ArrowBackIcon fontSize="medium" />
                    Back
                  </div>
                </div>
                <div style={{ margin: "20px", position: "relative" }}>
                  <form className="row" onSubmit={handleBikestep1}>
                    <input
                      autoComplete="off"
                      style={{ width: "100%" }}
                      type="Bikenumber"
                      className="search-field"
                      placeholder="Search Bike Brands"
                      name="Bikenumber"
                      required
                      value={BikeBrandName}
                      onChange={(e) => handleSearch(e.target.value)}
                    />
                    {/* <button className="default-btn">VIEW PRICES</button> */}
                  </form>
                  {searchResults.length > 0 ? (
                    <SearchResultContainer>
                      {MaphResults()}
                    </SearchResultContainer>
                  ) : null}
                </div>
                <div className="row" style={{ justifyContent: "center" }}>
                  {MapBikes()}
                </div>
              </div>
              <div style={{ height: "40px" }} />
              {/* <button
                type="submit"
                className="default-btn"
                style={{ width: "200px" }}
              >
                Others
              </button> */}
              <div
                className="row"
                style={{
                  justifyContent: "flex-end",
                }}
              ></div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default BikeStep2;
