import InsurerMenu from "../../insurerMenu";
function numberWithCommas(x) {
  return x && x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}
const InsuranceCoys = styled.div`
  background-color: #fff;
  min-height: 150px;
  width: 100%;
  border-radius: 4px;
  margin-bottom: 15px;
  margin-top: 15px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const InsureLogo = styled.img`
height:80px;
object-fit;


`;
const ContentSection = styled.div`
  height: 80%;
  // background-color: tomato;
  width: 90%;
  margin-left: 5px;
  padding-left: 10px;
  div {
    div {
      h3 {
        max-width: 150px;
      }
    }
  }
`;
const ButtonSection = styled.div`
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;
  background-color: tomato;
`;

const Button1 = styled.button`
  color: #31abfc;
  border: 1px solid #31abfc;
  background-color: #fff;
  border-radius: 4px;
  margin-bottom: 9px;
`;
const Button2 = styled.button`
  color: #fff;
  border: 1px solid tomato;
  background-color: tomato;
  border-radius: 4px;
`;
const Divider = styled.div`
  width: 1px;
  height: 100px;
  background-color: grey;
  margin-bottom: 10px;
  margin-top: 5px;
  margin-left: 10%;
`;
const OptionsLifeInsuranced = styled.div`
  height: 150px;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  background-color: #fff;
  text-align: center;
  padding: 10px;
  button {
    width: 100%;
  }
  h5 {
    color: rgb(138, 218, 180);
    text-align: left;
  }
  ul {
    text-align: left;
    cursor: pointer;

    li {
      margin-bottom: 8px;
      font-weight: 700;
    }
  }
`;
const Addsection = styled.div`
  min-height: 500px;
  width: 100%;
  background-color: #fff;
  margin-top: 15px;
  padding: 10px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const InsuranceCoysLifeInsuranced = (props) => {
  const {
    info: { logoImg, name, price, idv },
  } = props;
  const history = useHistory();
  const handlePaynow = () => {
    history.push({
      pathname: "/CheckoutPage-LifeInsurance",
      state: props.info,
    });
  };
  return (
    <InsuranceCoys>
      <InsureLogo src={logoImg} alt="img" />

      <ContentSection>
        <div className="verticalCenterRow justify-content-start">
          <div>
            <h3>{name}</h3>
            <a href="#">24hr claim settlement</a>
          </div>
          <Divider />
          <div className="FeaturesListing LifeInsuranceInsuranceAddons verticalCenterRow">
            <ul>
              <li>Life Cover : {idv ? `=N=` + numberWithCommas(idv) : null}</li>
              <li>60 yrs Max Limit: 99 yrs</li>
              <li>Claim settled 99.1%</li>
            </ul>
            <ul></ul>
          </div>
          <div
            style={{
              marginLeft: "auto",
              alignItems: "flex-end",
            }}
            className="horizontal-center-flex"
          >
            <Button1>N{numberWithCommas(price)}</Button1>
            {/* <small>PRODUCT DETAIL</small> */}
          </div>
        </div>
        {/* <Divider /> */}
        <div className="verticalCenterRow">
          <div
            style={{ marginLeft: "auto" }}
            className="horizontal-center-flex"
          >
            <Button2 onClick={handlePaynow}>PAY NOW</Button2>
          </div>
        </div>
        <div className="features-second">
          <ul>
            <li>Early Payout on terminal illness</li>
            <li>Waver of Premium Cover</li>
          </ul>
        </div>
      </ContentSection>
      {/* <ButtonSection>
        <Button1>N1,650</Button1>
        <small>PRODUCT DETAIL</small>
        <Button2>PAY NOW</Button2>
      </ButtonSection> */}
    </InsuranceCoys>
  );
};

const LifeInsurancestep2 = (props) => {
  const { match, history } = props;
  const [insuranceEntity, setInsuranceEntity] = React.useState([]);
  const [fetching, setFetching] = React.useState(false);

  const handleBack = () => {
    history.goBack();
  };
  const filterInsuranceEntity = (value) => {
    if (value === "all") {
      return FetchInsuranceEntity();
    }
    console.log(typeof value);
    value &&
      insuranceEntity.map((xxx) =>
        xxx.name === value ? setInsuranceEntity([xxx]) : null
      );
  };

  const filterInsuranceEntityByIDV = () => {};
  var FetchInsuranceEntity = async () => {
    console.log("called");
    setFetching(true);
    await axios
      .get(`/api/v1/fetchinsuranceEntity`)
      .then((response) => {
        setFetching(false);
        console.log(response.data);
        response.data.userData &&
          response.data.userData.length > 0 &&
          setInsuranceEntity(response.data.userData);
      })
      .catch((err) => {
        setFetching(false);
        if (err.message) {
          console.log(err.message);
        }
        console.log(err);
      });
  };

  React.useEffect(() => {
    FetchInsuranceEntity();
  }, []);
  const MapInsuranceEntity = () => {
    return insuranceEntity.map((xxx, index) => (
      <InsuranceCoysLifeInsuranced info={xxx} key={index} />
    ));
  };
  return (
    <div>
      <HeaderFixed />
      <div className="our-vision-area ptb-100 PricesSection">
        <div className="PricesHeaderSection row justify-content-center  align-items-center ">
          <p>
            iPolicyMart is acting only as a facilitator and claims settlement
            shall be at the sole discretion of the Insurer
          </p>
        </div>
        <div className="container ">
          <div style={{ height: "20px" }} />
          <div className="row justify-content-center">
            <div className="col-lg-9 col-sm-10 insurancCoys align-items-center">
              <div className="verticalCenterRow insuranceCoysHeader ">
                <div className="col-lg-1 col-sm-4">
                  <button>
                    <TuneIcon fontSize="medium" />
                  </button>
                </div>
                <div className="col-lg-3 col-sm-4">
                  <InsurerMenu
                    insuranceEntity={insuranceEntity}
                    filterInsuranceEntity={filterInsuranceEntity}
                  />
                </div>
                {/* <div className="col-lg-4 col-sm-4">
                  <IdvMenu />
                </div> */}
                <div>
                  {/* <button>
                    Plan Type
                    <ExpandMoreIcon
                      fontSize="medium"
                      style={{ marginLeft: "10px" }}
                    />
                  </button> */}
                  <PlanMenu />
                </div>
              </div>
              {fetching ? (
                <CircularProgress
                  color="secondary"
                  style={{ marginLeft: "45%" }}
                />
              ) : (
                MapInsuranceEntity()
              )}
              {/* <InsuranceCoysLifeInsuranced />
              <InsuranceCoysLifeInsuranced />
              <InsuranceCoysLifeInsuranced />
              <InsuranceCoysLifeInsuranced />
              <InsuranceCoysLifeInsuranced /> */}
            </div>
            <div className="col-lg-3 col-sm-6 insurancPlans">
              <OptionsLifeInsuranced>
                <h5>iPolicyMart Advantage</h5>
                <ul>
                  <li>-Lowest Price Guaranteed</li>
                  <li>-Certified Expert</li>
                  <li>-Dedicated Claim Assistance</li>
                </ul>
              </OptionsLifeInsuranced>
              <Addsection>
                <h3 style={{ color: "grey", marginTop: "50%" }}>Add Here</h3>
              </Addsection>
            </div>
          </div>
          <div className="disclaimer-Section">
            <h6>Disclaimer</h6>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Quis
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Quis
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
            </p>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default LifeInsurancestep2;
