import React from "react";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

export default function InsurerMenu(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [insurer, setInsurer] = React.useState("all");
  const { insuranceEntity, filterInsuranceEntity } = props;

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = (value) => {
    setAnchorEl(null);
    value && setInsurer(value.slice(0, 10));
    value && filterInsuranceEntity(value);
  };
  const MapInsuranceEntity = () => {
    return insuranceEntity.map((xxx) => (
      <MenuItem onClick={handleClose.bind(this, xxx.name)}>{xxx.name}</MenuItem>
    ));
  };

  return (
    <>
      <button onClick={handleClick}>
        insurrer({insurer}..){" "}
        <ExpandMoreIcon fontSize="medium" style={{ marginLeft: "10px" }} />
      </button>
      <Menu
        disableScrollLock={true}
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose.bind(this, "")}
      >
        <div style={{ width: "140px" }}></div>
        <MenuItem onClick={handleClose.bind(this, "all")}>All</MenuItem>
        {MapInsuranceEntity()}
      </Menu>
    </>
  );
}
