const OptionsCard = styled.div`
  height: 250px;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  background-color: #fff;
  text-align: center;
  padding: 10px;
  button {
    width: 100%;
  }
  ul {
    text-align: left;
    cursor: pointer;
    li {
      margin-bottom: 20px;
    }
  }
`;
const Addsection = styled.div`
  min-height: 500px;
  width: 100%;
  background-color: #fff;
  margin-top: 15px;
  padding: 10px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
