const HomeStep1 = (props) => {
  const dispatch = useDispatch();
  const { match, history } = props;
  const [homeOwnership, sethomeOwnership] = React.useState("");
  const [homeInsuranceType, sethomeInsuranceType] = React.useState("");
  const [otherElectronics, SetOtherElectronics] = React.useState("");
  const [otherPersonalItems, SetOtherPersonalItems] = React.useState("");
  const [errorAlert, setErrorAlert] = useState({ status: false, message: "" });
  const [formResponse, setFormResponse] = useState({
    Amount: "",
    house_address: "",
    house_name: "",
  });
  const handleBack = () => {
    history.goBack();
  };

  const HandleNext = (e) => {
    e.preventDefault();
    if (
      !formResponse.Amount ||
      !formResponse.house_address ||
      !formResponse.house_name ||
      // !homeInsuranceType ||
      !homeOwnership
    ) {
      console.log(formResponse);
      return setErrorAlert({
        status: true,
        message: "There is an error on this page, all fields were not filled",
      });
    } else {
      setErrorAlert({
        status: false,
        message: "",
      });
    }
    const NewFormresponse = {
      home_cost_estimation: formResponse.Amount,
      house_address: formResponse.house_address,
      house_name: formResponse.house_name,
      homeOwnership,
      otherElectronics,
      otherPersonalItems,
    };
    history.push({
      pathname: "/HomeStep2",
    });
    dispatch(SETHOMEINSURANCEPROCESSINFO(NewFormresponse));
  };

  return (
    <div>
      <HeaderFixed />
      <div
        className="our-vision-area ptb-100 homeSection"
        style={{ backgroundColor: "#fff" }}
      >
        <div className="container ">
          <div style={{ height: "20px" }} />
          <div className="row">
            <div className="col">
              <h2>Save Upto 80%* with Our Home insurance Plan!</h2>

              <p style={{ float: "right" }}>Step1 of 3</p>
              <div style={{ height: "40px" }} />
              <h3>Your policy details !</h3>
            </div>
          </div>
          <div style={{ height: "80px" }} />
          <div className="row" style={{}}>
            <form
              style={{
                width: "100%",

                justifyContent: "center",
                textAlign: "center",
              }}
              className="homeRegisterForm"
            >
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>What's your house number or name?</label>
                  <input
                    type="text"
                    class="form-control"
                    name="name"
                    value={formResponse.house_name}
                    onChange={(e) =>
                      setFormResponse({
                        ...formResponse,
                        house_name: e.target.value,
                      })
                    }
                    placeholder="House name or number"
                  />
                </div>
              </div>
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>What's your house address?</label>
                  <input
                    type="text"
                    value={formResponse.house_address}
                    onChange={(e) =>
                      setFormResponse({
                        ...formResponse,
                        house_address: e.target.value,
                      })
                    }
                    required
                    class="form-control"
                    name="address"
                    placeholder="House address"
                  />
                </div>
              </div>
              <div style={{ marginTop: "80px" }} />
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>Are you a tenant or home owner?</label>
                  <span
                    onClick={() => sethomeOwnership("owned")}
                    class="radioSelector"
                  >
                    <input
                      checked={homeOwnership === "owned"}
                      onChange={() => sethomeOwnership("owned")}
                      type="radio"
                      name="homeOwnership"
                      placeholder="House name or number"
                    />
                    I own the building
                  </span>
                  <span
                    onClick={() => sethomeOwnership("tenant")}
                    class="radioSelector"
                  >
                    <input
                      checked={homeOwnership === "tenant"}
                      onChange={() => sethomeOwnership("tenant")}
                      type="radio"
                      name="homeOwnership"
                    />
                    I live as a tenant
                  </span>
                </div>
              </div>
              {homeOwnership === "owned" ? (
                <div className="col-lg-8 col-sm-13">
                  <div class="form-group verticalCenterRow justify-content-lg-around ">
                    <label>I would like to insure?</label>
                    <span
                      onClick={() => sethomeInsuranceType("buildingOnly")}
                      class="radioSelector"
                    >
                      <input
                        checked={homeInsuranceType === "buildingOnly"}
                        onChange={() => sethomeInsuranceType("buildingOnly")}
                        type="radio"
                        name="homeInsuranceType"
                      />
                      Buildings only
                    </span>
                    <span
                      onClick={() => sethomeInsuranceType("contentOnly")}
                      class="radioSelector"
                    >
                      <input
                        checked={homeInsuranceType === "contentOnly"}
                        onChange={() => sethomeInsuranceType("contentOnly")}
                        type="radio"
                        name="homeInsuranceType"
                      />{" "}
                      Contents only
                    </span>
                    <span
                      onClick={() => sethomeInsuranceType("buildingAndContent")}
                      class="radioSelector"
                    >
                      <input
                        checked={homeInsuranceType === "buildingAndContent"}
                        onChange={() =>
                          sethomeInsuranceType("buildingAndContent")
                        }
                        type="radio"
                        name="homeInsuranceType"
                      />{" "}
                      The buildings and contents
                      <br />
                      (Combined Insurance )
                    </span>
                  </div>
                </div>
              ) : null}
              <div style={{ height: "80px" }} />
              <div
                className="row"
                style={{
                  backgroundColor: "rgb(243,247,253)",
                  padding: "10px",
                }}
              >
                <div className="col">
                  <h3>Home Contents !</h3>
                </div>
              </div>
              <div style={{ height: "70px" }} />
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>
                    Would you like to insure any <br />
                    home electronics or others worth over N200,000??
                  </label>

                  <span
                    onClick={() => SetOtherElectronics(true)}
                    class="radioSelector"
                  >
                    <input
                      checked={otherElectronics === true}
                      onChange={() => SetOtherElectronics(true)}
                      type="radio"
                      name="otherElectronics"
                      placeholder="House name or number"
                    />
                    yes
                  </span>
                  <span
                    onClick={() => SetOtherElectronics(false)}
                    class="radioSelector"
                  >
                    <input
                      checked={otherElectronics === false}
                      onChange={() => SetOtherElectronics(false)}
                      type="radio"
                      name="otherElectronics"
                    />
                    no
                  </span>
                </div>
              </div>
              <div style={{ height: "80px" }} />
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>
                    Would you like to insure any other
                    <br /> personal possessions ?
                  </label>

                  <span
                    onClick={() => SetOtherPersonalItems(true)}
                    class="radioSelector"
                  >
                    <input
                      checked={otherPersonalItems === true}
                      onChange={() => SetOtherPersonalItems(true)}
                      type="radio"
                      name="otherPersonalItems,"
                      placeholder="House name or number"
                    />
                    yes
                  </span>
                  <span
                    onClick={() => SetOtherPersonalItems(false)}
                    class="radioSelector"
                  >
                    <input
                      checked={otherPersonalItems === false}
                      onChange={() => SetOtherPersonalItems(false)}
                      type="radio"
                      name="otherPersonalItems"
                    />
                    no
                  </span>
                </div>
              </div>
              <div style={{ height: "80px" }} />
              <div className="col-lg-8 col-sm-13">
                <div class="form-group verticalCenterRow justify-content-lg-around ">
                  <label>
                    How much do you think everything
                    <br /> in your home would cost?
                  </label>
                  <input
                    value={formResponse.Amount}
                    onChange={(e) =>
                      setFormResponse({
                        ...formResponse,
                        Amount: e.target.value,
                      })
                    }
                    type="number"
                    class="form-control"
                    name="name"
                    placeholder="Enter amount"
                  />
                  =N=
                </div>
              </div>

              <div style={{ height: "80px" }} />
              {errorAlert.status ? (
                <div className="col-lg-6">
                  <Alert style={{ marginBottom: "10px" }} severity="error">
                    {errorAlert.message}
                  </Alert>
                </div>
              ) : null}
              <div style={{ height: "80px" }} />
              <button onClick={HandleNext} type="submit" class="default-btn ">
                Next <ArrowForwardIosIcon fontSize="medium" />
              </button>
            </form>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default HomeStep1;
