import React, { useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import ArrowBackIcon from "@material-ui/icons/ArrowLeft";
import TravellStepperApp2 from "./TravelStepperApp2";

const TravelStep2 = (props) => {
  const [carnumber, setCarnumber] = useState("");
  const { match, history } = props;
  const handleCarstep1 = (e) => {
    e.preventDefault();
    // history.push("/");
  };
  const handleBack = () => {
    history.goBack();
  };
  const handleNext = () => {
    history.push("TravelStep3");
  };
  return (
    <div>
      <HeaderFixed />
      <div className="our-vision-area ptb-100 Trans">
        <div style={{ height: "10px" }} />
        <div className="container">
          <div
            className="row align-items-center"
            style={{ justifyContent: "center", alignItems: "flex-start" }}
          >
            <div className="col-lg-10">
              <div className="vision-content">
                <h1>Explore the world without any worries</h1>
                {/* <h3>
                  Best travel insurance Policies designed for all your needs
                  ||Now covers Covid-19
                </h3> */}
                <p>
                  Best travel insurance Policies designed for all your needs
                  ||Now covers Covid-19
                </p>

                <div
                  className="row"
                  style={{
                    justifyContent: "center",
                    backgroundColor: "rgb(243,244,247)",
                    borderRadius: "10px",

                    boxSizing: "border-box",
                    marginTop: "20px",
                    paddingTop: "60px",
                  }}
                >
                  <div
                    style={{ margin: "20px" }}
                    className="row"
                    onSubmit={handleCarstep1}
                  >
                    {/* <input
                      type="carnumber"
                      className="search-field"
                      placeholder="Enter Car Number"
                      name="carnumber"
                      required
                      value={carnumber}
                      onChange={(e) => setCarnumber(e.target.value)}
                    /> */}
                    <button
                      onClick={handleBack}
                      style={{
                        maxHeight: "80px",
                        paddingLeft: "20px",
                        paddingRight: "20px",
                        color: "#31abfc",
                        marginBottom: "20px",

                        backgroundColor: "#fff",
                      }}
                      className="default-btn"
                    >
                      <ArrowBackIcon fontSize="small" />
                      BACK
                    </button>
                    <TravellStepperApp2 />
                    <button
                      onClick={handleNext}
                      style={{
                        maxHeight: "80px",
                        paddingLeft: "40px",
                        paddingRight: "40px",
                      }}
                      className="default-btn"
                    >
                      VIEW PLANS
                    </button>
                  </div>
                </div>
                <div className="vision-list"></div>
              </div>
              <div style={{ height: "100px" }} />

              <div
                className="row"
                style={{
                  justifyContent: "flex-end",
                }}
              ></div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default TravelStep2;
