import React, { useState } from "react";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import styled from "styled-components";
import SearchIcon from "@material-ui/icons/Search";
import CancelIcon from "@material-ui/icons/Cancel";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import Switch from "@material-ui/core/Switch";
import { DateRangePicker } from "react-dates";
// import "react-dates/initialize";
// import "react-dates/lib/css/_datepicker.css";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";

import { withStyles } from "@material-ui/core/styles";
import Moment from "react-moment";

const AntSwitch = withStyles((theme) => ({
  root: {
    width: 21,
    height: 11,
    padding: 3,
    display: "flex",
  },
  switchBase: {
    padding: 0.2,
    color: theme.palette.grey[500],
    "&$checked": {
      transform: "translateX(12px)",
      color: theme.palette.common.white,
      "& + $track": {
        opacity: 1,
        backgroundColor: theme.palette.primary.main,
        borderColor: theme.palette.primary.main,
      },
    },
  },
  thumb: {
    width: 12,
    height: 12,
    boxShadow: "none",
  },
  track: {
    border: `1px solid ${theme.palette.grey[500]}`,
    borderRadius: 16 / 2,
    opacity: 1,
    backgroundColor: theme.palette.common.white,
  },
  checked: {},
}))(Switch);

const TravvelersSlector = styled.div`
  background-color: #fff;
  height: 150px;
  overflow-y: auto;
  width: 40%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  text-align: center;
  margin-left: 40%;
  p {
    font-size: 10px;
    text-align: left;
  }
`;
const DatesSelector = styled.div`
  background-color: #fff;
  min-height: 100px;
  overflow-y: auto;
  margin-top: 10px;
  width: 60%;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-sizing: border-box;
  text-align: center;
  position: absolute;
  right: 0;
  p {
    font-size: 10px;
    text-align: left;
  }
`;
const TravelBar = styled.div`
  width: 600px;
  height: 80px;

  //   background-color: #fff;
  display: flex;
  flex-direction: row;
  @media (max-width: 778px) {
    width: 100vw;
  }
`;
const DropDownContainer = styled.div`
width:100%
position:absolute;
min-height:200px;
top:80px;

display:flex;
flex-direction:column;
transition:all 1.2s ease;

`;
const PopularCountries = styled.div`
  height: 200px;
  width: 98%;
  margin-top: 10px;
  background-color: #fff;
  padding: 5px;
  display: flex;
  flex-direction: column;

  box-sizing: border-box;
`;
const SelectedCountriesSection = styled.div`
  background-color: dodgerblue;
  height: 100px;
  overflow-y: auto;
  width: 40%;
  padding: 5px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  text-align: center;
`;

const CustomeSelectedItem = styled.p`
  background-color: #ffff;
  flex-direction: row;
  align-items: center;
  min-height: 20px;
  //  min-width: 10px;
  flex-wrap: wrap;
  padding: 3px;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  border-radius: 30px;
  font-size: 8px;
  margin: auto;
`;
const ContentSection = styled.div`
  width: 100%;

  height: 90%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
`;

const TravellStepperApp2 = () => {
  const [selected, setSelected] = React.useState([]);
  const [StartDate, setStartDate] = useState(null);
  const [EndDate, setEndDate] = useState(null);
  const [selectedCountries, setSelectedCountries] = React.useState([]);
  const [focusedStepper, setfocusedSteppe] = React.useState("country");
  const [focus, setFocus] = useState("startDate");
  const [mobile, setMobile] = useState();

  //   const { startDate, endDate } = dateRange;
  const Dateformat = (date) => {
    return <Moment format="YYYY/MM/DD">{date}</Moment>;
  };
  const MapselectedCountries = (props) => {
    const handleDeleteItem = (xxx) => {
      setSelectedCountries(selectedCountries.filter((item) => item !== xxx));
    };
    return (
      selectedCountries.length > 0 &&
      selectedCountries.map((xxx) => (
        <CustomeSelectedItem>
          {xxx}{" "}
          <CancelIcon
            onClick={handleDeleteItem.bind(this, xxx)}
            fontSize="small"
            style={{ fontSize: "10px", marginLeft: "10px", cursor: "pointer" }}
          />
        </CustomeSelectedItem>
      ))
    );
  };
  const HandleMySelection = (value) => {
    selectedCountries.length < 7 &&
      !selectedCountries.some((item) => item === value) &&
      setSelectedCountries([...selectedCountries, value]);
  };

  return (
    <div style={{ position: "relative", marginBottom: "40px" }}>
      <TravelBar>
        <div
          style={{
            padding: "10px",
            paddingTop: "14px",
            boxSizing: "border-box",
            // border: "0.4px solid grey",
            width: "40%",
            textAlign: "left",
            cursor: "pointer",
            margin: "1px",

            position: "relative",
            backgroundColor: "#fff",
          }}
        >
          <p
            style={{
              lineHeight: "100%",
              fontSize: "14px",
            }}
          >
            You are just <br />
            <br />
            <b> one step away</b>
          </p>
          <div
            style={{
              marginTop: "auto",
              width: "100%",
            }}
          >
            {/* <SearchIcon fontSize="small" style={{ color: "#fff" }} /> */}
          </div>
        </div>
        <div
          style={{
            padding: "10px",
            paddingTop: "20px",
            boxSizing: "border-box",
            width: "27%",
            textAlign: "left",
            cursor: "pointer",
            // border: "0.4px solid grey",
            margin: "1px",
            backgroundColor: "dodgerblue",

            color: "#fff",
          }}
        >
          <p
            style={{
              lineHeight: "100%",
              fontSize: "14px",
            }}
          >
            Mobile number <br />
            <br />
            <b> </b>
          </p>
        </div>
        <div
          style={{
            // border: "0.4px solid grey",
            padding: "10px",
            paddingTop: "20px",
            boxSizing: "border-box",
            width: "33%",
            textAlign: "left",
            cursor: "pointer",

            margin: "1px",
            backgroundColor: "#fff",
          }}
        >
          <p
            style={{
              lineHeight: "100%",
              fontSize: "14px",
            }}
          >
            <br />

            <PhoneInput
              placeholder="Enter Mobile number"
              value={mobile}
              onChange={setMobile}
            />
          </p>
        </div>
      </TravelBar>
    </div>
  );
};

export default TravellStepperApp2;
