import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import TopMenu from "./TopMenu";

const Header = () => {
  const [isSticky, setisSticky] = useState("");
  const [show, setshow] = useState("");
  const [collapsed, setCollapsed] = useState("collapsed");

  const handleNavColapse = () => {
    //navbar toggle
    show !== "show" && setshow("show");
    show === "show" && setshow("");
    collapsed !== "collapsed" && setCollapsed("");
    collapsed === "collapsed" && setCollapsed("collapsed");
  };
  const handleScroll = () => {
    //hides navbar on scroll
    const position = window.pageYOffset;

    position > 200 && setisSticky("is-sticky");
    position < 300 && setisSticky("");
  };
  useEffect(() => {
    window.addEventListener("scroll", handleScroll, { passive: true });

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <header className="header-area fixed-top">
      <div className="top-header-area">
        <div className="container">
          <div className="row align-items-center">
            <div className="col-lg-6 col-md-4 col-sm-6">
              <div className="header-content-left">
                <p>Welcome To Ipolicymart Insurance!</p>
              </div>
            </div>
            <div className="col-lg-6 col-md-8 col-sm-6">
              <ul className="header-content-right">
                <li>
                  <a href="tel:+822456974">
                    <i className="bx bx-phone-call" />
                    Call Us For Inquiry: +1000000000
                  </a>
                </li>

                <li>
                  <a href="cdn-cgi/l/email-protection#87efe2ebebe8c7e1ebe2ffe6a9e4e8ea">
                    <i className="bx bx-envelope" />
                    Email:
                    <span
                      className="__cf_email__"
                      data-cfemail="3a525f5656557a5c565f425b14595557"
                    >
                      help@Ipolicymart.com
                    </span>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div id="navbar" className={`navbar-area nav-style-two ${isSticky}`}>
        <div className="main-nav">
          <nav className="navbar navbar-expand-lg navbar-light">
            <div className="container">
              <Link to="/" className="navbar-brand">
                <img
                  style={{ height: "30px" }}
                  src="images/ipo2.png"
                  alt="logo"
                />
              </Link>
              <button
                onClick={handleNavColapse}
                className={`navbar-toggler navbar-toggler-right ${collapsed} `}
                type="button"
                data-toggle="collapse"
                data-target="#navbarSupportedContent"
                aria-controls="navbarSupportedContent"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="icon-bar top-bar" />
                <span className="icon-bar middle-bar" />
                <span className="icon-bar bottom-bar" />
              </button>
              <div
                className={`collapse navbar-collapse ${show} `}
                id="navbarSupportedContent"
              >
                <ul className="navbar-nav m-auto">
                  <li className="nav-item">
                    <a className="nav-link" href="/#">
                      Home
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link" href="about">
                      About
                    </a>
                  </li>
                  {/* <li className="nav-item">
                    <a className="nav-link" href="/#">
                      Pages
                     
                    </a>
                  </li> */}
                  <li className="nav-item">
                    <a className="nav-link" href="/#">
                      Insurance <i className="bx bx-chevron-down" />
                    </a>
                    <ul className="dropdown-menu">
                      <li className="nav-item">
                        <a className="nav-link" href>
                          Corporate Insurance
                          {/* <i className="bx bx-chevron-down" /> */}
                        </a>
                        {/* <ul className="dropdown-menu">
                          <li className="nav-item">
                            <a className="nav-link" href="index">
                              lorem
                            </a>
                          </li>
                          <li className="nav-item">
                            <a className="nav-link " href="index2">
                              lorem
                            </a>
                          </li>
                          <li className="nav-item">
                            <a className="nav-link" href="index3">
                              lorem
                            </a>
                          </li>
                        </ul> */}
                      </li>
                      <li className="nav-item">
                        <a className="nav-link" href>
                          personal insurance
                          <i className="bx bx-chevron-down" />
                        </a>
                        <ul className="dropdown-menu">
                          <li className="nav-item">
                            <a
                              // to="/#"
                              className="nav-link"
                              // href="car-insurance"
                            >
                              auto Insurance
                              <i className="bx bx-chevron-down" />
                            </a>
                            <ul className="dropdown-menu dropdown-menu-right">
                              <li className="nav-item">
                                <Link
                                  to="/Auto-insurance"
                                  className="nav-link"
                                  href="car-insurance"
                                >
                                  auto Insurance
                                </Link>
                              </li>
                              <li className="nav-item">
                                <Link
                                  // to="/CarStep1"
                                  className="nav-link"
                                  href="car-insurance"
                                >
                                  Comprehensive
                                </Link>
                              </li>
                              <li className="nav-item">
                                <Link
                                  // to="/CarStep1"
                                  className="nav-link"
                                  href="car-insurance"
                                >
                                  Third Party Fire and Theft
                                </Link>
                              </li>
                              <li className="nav-item">
                                <Link
                                  // to="/CarStep1"
                                  className="nav-link"
                                  href="car-insurance"
                                >
                                  Third Party
                                </Link>
                              </li>
                            </ul>
                          </li>
                          <li className="nav-item">
                            <Link
                              to="/HealthInsurance-Step1"
                              className="nav-link"
                              href="car-insurance"
                            >
                              Health Insurance
                            </Link>
                          </li>
                          <li className="nav-item">
                            <li className="nav-item">
                              <Link className="nav-link" to="/TravelStep1">
                                Travel Insurance
                              </Link>
                            </li>
                          </li>
                          <li className="nav-item">
                            <Link
                              to="/HomeStep1"
                              className="nav-link"
                              href="car-insurance"
                            >
                              Home Insurance
                            </Link>
                          </li>
                          <li className="nav-item">
                            <Link
                              to="/LifeInsurance-Step1"
                              className="nav-link"
                              href="car-insurance"
                            >
                              Life Insurance
                            </Link>
                          </li>
                        </ul>
                      </li>
                    </ul>
                  </li>
                  {/* <li className="nav-item">
                    <a className="nav-link" href="/#">
                      Portfolio
                    </a>
                  </li> */}
                  <li className="nav-item">
                    <a className="nav-link" href="/#">
                      File a Claim
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link" href="contact">
                      Contact Us
                    </a>
                  </li>
                </ul>
                <div className="others-option">
                  <div className="option-item">
                    <i className="search-btn bx bx-search" />
                  </div>
                  <div className="subscribe">
                    <a className="default-btn" href="/#">
                      Get A Quote
                    </a>
                  </div>
                  <div className="option-item navbtn ">
                    <TopMenu />
                  </div>
                </div>
              </div>
            </div>
          </nav>
        </div>
      </div>
    </header>
  );
};

export default Header;
