import React, { useState } from "react";
import Footer from "../../Footer";
import Header from "../../Header";
import HeaderFixed from "../../Header_fixed";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import ArrowBackIcon from "@material-ui/icons/ArrowLeft";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import TuneIcon from "@material-ui/icons/Tune";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import styled from "styled-components";
import { useDispatch } from "react-redux";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import DoneIcon from "@material-ui/icons/Done";
import { SETLIFEINSURANCEPROCESSINFO } from "../../../redux/action";
import Alert from "@material-ui/lab/Alert";
const InsuranceCoys = styled.div`
  background-color: #fff;
  min-height: 150px;
  width: 100%;
  border-radius: 4px;
  margin-bottom: 15px;
  margin-top: 15px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;
const InsureLogo = styled.img`
height:80px;
object-fit;


`;
const ContentSection = styled.div`
  height: 80%;
  // background-color: tomato;
  width: 90%;
  margin-left: 5px;
  padding-left: 10px;
`;
const ButtonSection = styled.div`
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;
  background-color: tomato;
`;

const Button1 = styled.button`
  color: #31abfc;
  border: 1px solid #31abfc;
  background-color: #fff;
  border-radius: 4px;
  margin-bottom: 9px;
`;
const Button2 = styled.button`
  color: #fff;
  border: 1px solid #31abfc;
  background-color: #31abfc;
  border-radius: 4px;
`;
const Divider = styled.div`
  width: 100%;
  height: 1px;
  background-color: grey;
  margin-bottom: 10px;
  margin-top: 5px;
`;
const OptionsCard = styled.div`
  height: 250px;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  background-color: #fff;
  text-align: center;
  padding: 10px;
  button {
    width: 100%;
  }
  ul {
    text-align: left;
    cursor: pointer;
    li {
      margin-bottom: 20px;
    }
  }
`;
const Addsection = styled.div`
  min-height: 500px;
  width: 100%;
  background-color: #fff;
  margin-top: 15px;
  padding: 10px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
`;

const LifeInsuranceStep1 = (props) => {
  const dispatch = useDispatch();
  const { match, history } = props;
  const [formResponse, setFormResponse] = React.useState({
    smoker: "",
    cover_type: "",
    title: "",
    firstName: "",
    surnName: "",
    email: "",
    dob: "",
    coverDuration: "",
    mobilePhone: "",
    alcoholic_drinks: "",
  });

  const [errorAlert, setErrorAlert] = useState({ status: false, message: "" });
  const handleBack = () => {
    history.goBack();
  };
  function validateEmail(email) {
    const re =
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }
  const handleNext = (e) => {
    e.preventDefault();
    if (
      !formResponse.smoker ||
      !formResponse.cover_type ||
      !formResponse.title ||
      !formResponse.firstName ||
      !formResponse.email ||
      !formResponse.coverDuration ||
      !formResponse.mobilePhone ||
      !formResponse.alcoholic_drinks
    ) {
      console.log(formResponse);
      return setErrorAlert({
        status: true,
        message: "There is an error on this page, all fields were not filled",
      });
    } else {
      setErrorAlert({
        status: false,
        message: "",
      });
    }
    if (!validateEmail(formResponse.email)) {
      return setErrorAlert({
        status: true,
        message: "Invalid email provided",
      });
    }

    history.push("/LifeInsurance-Step2");
    dispatch(SETLIFEINSURANCEPROCESSINFO(formResponse));
  };
  return (
    <div>
      <HeaderFixed />
      <div
        className="our-vision-area ptb-100 homeSection"
        style={{ backgroundColor: "#fff" }}
      >
        <div className="about-area ptb-70" style={{ backgroundColor: "#fff" }}>
          <div className="container">
            <div className="row align-items-start">
              <div className="col-lg-6">
                <div className="about-content">
                  <span>Life insurance</span>
                  <p style={{ float: "right" }}></p>
                  <h2>Take a moment to answer the following questions</h2>
                  <form>
                    <p>Your policy details</p>
                    {/* <div className="Q-container">
                      <h6>What do you need insurance for</h6>
                      <div className="col-lg-6 col-sm-6 OptionCard verticalCenterRow">
                        <input type="radio" /> <p>Mortgage</p>
                      </div>
                      <div className="col-lg-6 col-sm-6 OptionCard verticalCenterRow">
                        <input type="radio" /> <p>Living costs</p>
                      </div>
                      <div className="col-lg-6 col-sm-6 OptionCard verticalCenterRow">
                        <input type="radio" /> <p>Mortgage and Living costs</p>
                      </div>
                    </div> */}
                    <div className="Q-container">
                      <h6>What type of cover do you need ?</h6>
                      <div
                        onClick={() =>
                          setFormResponse({
                            ...formResponse,
                            cover_type: "Term life Insurance",
                          })
                        }
                        className={`col-lg-6 col-sm-6 OptionCard verticalCenterRow ${
                          formResponse.cover_type === "Term life Insurance" &&
                          "active"
                        } `}
                      >
                        <input
                          checked={
                            formResponse.cover_type === "Term life Insurance"
                          }
                          name="cover_type"
                          type="radio"
                        />{" "}
                        <p>Term life Insurance </p>
                      </div>
                      <div
                        onClick={() =>
                          setFormResponse({
                            ...formResponse,
                            cover_type: "Permanent Insurance",
                          })
                        }
                        className={`col-lg-6 col-sm-6 OptionCard verticalCenterRow ${
                          formResponse.cover_type === "Permanent Insurance" &&
                          "active"
                        } `}
                      >
                        <input
                          checked={
                            formResponse.cover_type === "Permanent Insurance"
                          }
                          name="cover_type"
                          type="radio"
                        />{" "}
                        <p>Permanent Insurance </p>
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6>Are you a smoker?:</h6>
                      <div
                        onClick={() =>
                          setFormResponse({ ...formResponse, smoker: true })
                        }
                        className={`col-lg-6 col-sm-6 OptionCard verticalCenterRow ${
                          formResponse.smoker === true && "active"
                        } `}
                      >
                        <input
                          checked={formResponse.smoker === true}
                          name="smoker"
                          type="radio"
                        />{" "}
                        <p>Yes</p>
                      </div>
                      <div
                        onClick={() =>
                          setFormResponse({ ...formResponse, smoker: false })
                        }
                        className={`col-lg-6 col-sm-6 OptionCard verticalCenterRow ${
                          formResponse.smoker === false && "active"
                        } `}
                      >
                        <input
                          checked={formResponse.smoker === false}
                          name="smoker"
                          type="radio"
                        />{" "}
                        <p>No</p>
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6>How much cover do you need?</h6>
                      <div
                        className={`col-lg-6 col-sm-6 OptionCard no-border verticalCenterRow  `}
                      >
                        =N= <input name="smoker" type="number" />
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6>How many years do you need your policy to last?</h6>
                      <div
                        className={`col-lg-6 col-sm-6 OptionCard no-border verticalCenterRow  `}
                      >
                        <input
                          name="duration"
                          type="number"
                          value={formResponse.coverDuration}
                          onChange={(e) =>
                            setFormResponse({
                              ...formResponse,
                              coverDuration: e.target.value,
                            })
                          }
                        />
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6>Title:</h6>
                      <div
                        onClick={() =>
                          setFormResponse({ ...formResponse, title: "Mr" })
                        }
                        className={`col-lg-6 col-sm-6 OptionCard verticalCenterRow ${
                          formResponse.title === "Mr" && "active"
                        } `}
                      >
                        <input
                          checked={formResponse.title === "Mr"}
                          name="title"
                          type="radio"
                        />{" "}
                        <p>Mr</p>
                      </div>
                      <div
                        onClick={() =>
                          setFormResponse({ ...formResponse, title: "Mrs" })
                        }
                        className={`col-lg-6 col-sm-6 OptionCard verticalCenterRow ${
                          formResponse.title === "Mrs" && "active"
                        } `}
                      >
                        <input
                          checked={formResponse.title === "Mrs"}
                          name="title"
                          type="radio"
                        />{" "}
                        <p>Mrs</p>
                      </div>
                      <div
                        onClick={() =>
                          setFormResponse({ ...formResponse, title: "Ms" })
                        }
                        className={`col-lg-6 col-sm-6 OptionCard verticalCenterRow ${
                          formResponse.title === "Ms" && "active"
                        } `}
                      >
                        <input
                          checked={formResponse.title === "Ms"}
                          name="title"
                          type="radio"
                        />{" "}
                        <p>Ms</p>
                      </div>
                      <div
                        onClick={() =>
                          setFormResponse({ ...formResponse, title: "Miss" })
                        }
                        className={`col-lg-6 col-sm-6 OptionCard verticalCenterRow ${
                          formResponse.title === "Miss" && "active"
                        } `}
                      >
                        <input
                          checked={formResponse.title === "Miss"}
                          name="title"
                          type="radio"
                        />{" "}
                        <p>Miss</p>
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6>First Name?</h6>
                      <div
                        className={`col-lg-6 col-sm-6 OptionCard no-border verticalCenterRow  `}
                      >
                        <input
                          style={{ width: "100%" }}
                          type="texr"
                          value={formResponse.firstName}
                          onChange={(e) =>
                            setFormResponse({
                              ...formResponse,
                              firstName: e.target.value,
                            })
                          }
                        />
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6>Surname?</h6>
                      <div
                        className={`col-lg-6 col-sm-6 OptionCard no-border verticalCenterRow  `}
                      >
                        <input
                          style={{ width: "100%" }}
                          type="texr"
                          value={formResponse.surnName}
                          onChange={(e) =>
                            setFormResponse({
                              ...formResponse,
                              surnName: e.target.value,
                            })
                          }
                        />
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6>Email?</h6>
                      <div
                        className={`col-lg-7 col-sm-6 OptionCard no-border verticalCenterRow  `}
                      >
                        <input
                          style={{ width: "100%" }}
                          type="texr"
                          value={formResponse.email}
                          onChange={(e) =>
                            setFormResponse({
                              ...formResponse,
                              email: e.target.value,
                            })
                          }
                        />
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6>Phone number?</h6>
                      <div
                        className={`col-lg-7 col-sm-6 OptionCard no-border verticalCenterRow  `}
                      >
                        <input
                          style={{ width: "100%" }}
                          type="texr"
                          value={formResponse.mobilePhone}
                          onChange={(e) =>
                            setFormResponse({
                              ...formResponse,
                              mobilePhone: e.target.value,
                            })
                          }
                        />
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6> What is your date of birth ?</h6>
                      <div
                        className={`col-lg-5 col-sm-6 OptionCard no-border verticalCenterRow  `}
                      >
                        <input
                          type="date"
                          id="datemax"
                          name="datemax"
                          max="1979-12-31"
                          onChange={(e) =>
                            setFormResponse({
                              ...formResponse,
                              dob: e.target.value,
                            })
                          }
                        />

                        {/* <input
                          placeholder="MM"
                          style={{ width: "13%" }}
                          type="text"
                          value={formResponse.dob.month}
                        
                        /> */}
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6>What's your height ? (ft)</h6>
                      <div
                        className={`col-3  OptionCard no-border verticalCenterRow  `}
                      >
                        <input
                          style={{ width: "100%" }}
                          name="smoker"
                          type="texr"
                        />
                      </div>
                    </div>
                    <div className="Q-container">
                      <h6>How many alcoholic drinks do you take per week?</h6>
                      <div
                        className={`col-3  OptionCard no-border verticalCenterRow  `}
                      >
                        <input
                          style={{ width: "100%" }}
                          name="smoker"
                          type="texr"
                          value={formResponse.alcoholic_drinks}
                          onChange={(e) =>
                            setFormResponse({
                              ...formResponse,
                              alcoholic_drinks: e.target.value,
                            })
                          }
                        />
                      </div>
                    </div>
                    <div style={{ height: "80px" }} />
                    {errorAlert.status ? (
                      <Alert style={{ marginBottom: "10px" }} severity="error">
                        {errorAlert.message}
                      </Alert>
                    ) : null}
                    <button
                      style={{ float: "right" }}
                      type="submit"
                      class="default-btn "
                      onClick={handleNext}
                    >
                      Get quotes <ArrowForwardIosIcon fontSize="medium" />
                    </button>
                  </form>
                </div>
              </div>
              <div className="col-lg-6">
                <div
                  className="about-img fadeInRight"
                  style={{
                    animationName: "none",
                    visibility: "visible",
                    animationDelay: "0.1s",
                  }}
                >
                  <img src="images/about-img.png" alt="Image" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default LifeInsuranceStep1;
